import {
  Box,
  Button,
  Grid,
  MenuItem,
  Paper,
  Select,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import React, { useEffect, useRef, useState } from "react";
import CustomFormLabel from "./Common/CustomFormLabel";
import CustomTextField from "./Common/CustomTextField";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import "./Quill.css";
import axios from "axios";
import { API_PATHS } from "../utils/apiUtils";
import DragDrop from "./Common/DragDrop";

export const AddProduct = () => {
  const nameRef = useRef();
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const colorRef = useRef();
  const descriptionRef = useRef();
  const iconRef = useRef();
  const didYouKnowRef = useRef();
  const rootFolderNameRef = useRef();
  const fileNameRef = useRef();

  const [subCategories, setSubCategories] = useState([]);
  const [selectedSubCategory, setSelectedSubCategory] = useState();

  const getAllCategories = async () => {
    const response = await axios.get(API_PATHS.GET_ALL_CATEGORIES);
    const categoriesData = response.data.result.map((item) => ({
      id: item._id,
      name: item.name,
    }));

    setCategories(categoriesData);
  };
  console.log("selectedCategory-->", selectedCategory);

  useEffect(() => {
    getAllCategories();
  }, []);

  const handleChangeCategory = async (e) => {
    setSelectedCategory(e.target.value);
    const subCategoriesData = await axios.post(
      API_PATHS.GET_PEN_SUB_CATEGORIES,
      { category_id: e.target.value }
    );

    console.log("subCategory-->", subCategoriesData);

    const temp = subCategoriesData.data.result.map((item) => ({
      id: item._id,
      category: item.category,
    }));

    setSubCategories(temp);
  };

  const handleSubmit = async () => {
    const data = {
      name: nameRef.current.value,
      category_type: selectedSubCategory,
      main_category_type: selectedCategory,
      color: colorRef.current.value,
      description: descriptionRef.current.value,
      icon: iconRef.current.value,
      did_you_know: didYouKnowRef.current.value,
      // file_name: fileNameRef.current.value,
      // root_folder_name: rootFolderNameRef.current.value,
    };

    console.log("add product data--->", data);

    const res = await axios.post(API_PATHS.ADD_PRODUCT, data);
  };

  return (
    <Box sx={{ width: "calc(100% - 230px)", m: 2 }}>
      {/* <Box
          sx={{
            flexDirection: "row-reverse",
            display: "flex",
          }}
        >
          <Button variant="contained">Add</Button>
        </Box> */}

      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Add Product</Typography>
      </Box>
      <Paper
        elevation={3}
        sx={{
          paddingY: 2,
          paddingX: 4,
          boxShadow:
            "0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12)",
        }}
        variant="outlined"
      >
        {/* ------------------------------------------------------------------------------------------------ */}
        {/* Basic Layout */}
        {/* ------------------------------------------------------------------------------------------------ */}
        <Grid container>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-title" sx={{ mt: 0 }}>
              Name
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-name"
              placeholder="John Deo"
              inputRef={nameRef}
              fullWidth
            />
          </Grid>

          <Grid item xs={12} alignItems="center">
            <CustomFormLabel htmlFor="demo-simple-select">
              Category type
            </CustomFormLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={selectedCategory}
              onChange={(e) => handleChangeCategory(e)}
              color="error"
              fullWidth
              size="small"
            >
              {categories.map((item) => (
                <MenuItem key={item.id} value={item.id}>
                  {item.name}
                </MenuItem>
              ))}
            </Select>
          </Grid>
          <Grid item xs={12} alignItems="center">
            <CustomFormLabel htmlFor="demo-simple-select">
              Sub-category type
            </CustomFormLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={selectedSubCategory}
              onChange={(e) => setSelectedSubCategory(e.target.value)}
              color="error"
              fullWidth
              size="small"
            >
              {subCategories.map((item) => (
                <MenuItem key={item.id} value={item.id}>
                  {item.category}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-color">Color</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-color"
              placeholder="color"
              fullWidth
              inputRef={colorRef}
            />
          </Grid>

          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-description">
              Description
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <Paper variant="outlined">
              <CustomTextField
                // value={quillText}
                // onChange={(value) => setQuillText(value)}
                inputRef={descriptionRef}
                placeholder="Description"
                style={{ width: "100%" }}
              />
            </Paper>
          </Grid>
          {/* 4 */}
          {/* <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-phone">Description</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              //   value={blogDate}
              type="date"
              id="fs-date"
              placeholder="John Deo"
              fullWidth
            />
          </Grid> */}
          {/* 5 */}
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-message">Icon</CustomFormLabel>
          </Grid>

          <Grid item xs={12}>
            <CustomTextField
              id="bl-icon"
              fullWidth
              inputRef={iconRef}
              //   onChange={handleChooseImage}
            />
            {/* <Button onClick={async () => console.log(await uploadImageToFirebase(blogImage))}>
            Upload
          </Button> */}
          </Grid>

          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-did">Did you know</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField id="bl-did" fullWidth inputRef={didYouKnowRef} />
          </Grid>
          {/* <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-filename">File name</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-filename"
              fullWidth
              inputRef={fileNameRef}
            />
          </Grid>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-root">
              Root folder name
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-root"
              fullWidth
              inputRef={rootFolderNameRef}
            />
          </Grid> */}
          {/* <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-root">Product image</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <DragDrop />
          </Grid> */}

          {/* <Grid item xs={12}>
            <Autocomplete
              multiple
              id="blog-tags"
              options={[]}
              value={selectedTags}
              freeSolo
              onChange={(e, value) => setSelectedTags(value)}
              renderInput={(params) => (
                <CustomTextField {...params} placeholder="Add your tags..." />
              )}
            />
          </Grid> */}

          <Grid item xs={12} mt={3}>
            <Button
              variant="contained"
              color="primary"
              sx={{ fontWeight: "600", background: "#ed1b24" }}
              onClick={handleSubmit}
            >
              Submit
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  );
};
