import {
  Box,
  Button,
  Grid,
  MenuItem,
  Paper,
  Select,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import React, { useEffect, useRef, useState } from "react";
import CustomFormLabel from "./Common/CustomFormLabel";
import CustomTextField from "./Common/CustomTextField";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import "./Quill.css";
import axios from "axios";
import { API_PATHS, API_URL } from "../utils/apiUtils";
import DragDrop from "./Common/DragDrop";
import { FileUploader } from "react-drag-drop-files";
import { useParams } from "react-router-dom";

export const EditCategory = () => {
  const router = useParams();
  const fileTypes = ["JPG", "PNG", "GIF"];
  const [name, setName] = useState();
  const [file, setFile] = useState(null);
  const [updateCategoryObj, setUpdateCategoryObj] = useState(null);
  const updateCategoryId = router.id;
  console.log("category update id --->", updateCategoryId);

  const handleSubmit = async () => {
    try {
      const res = await axios.patch(
        API_PATHS.UPDATE_CATEGORY + `/${updateCategoryId}`,
        { name }
      );
      alert("Category updated successfully!");
    } catch (err) {
      alert("Error, while updating category");
    }
  };

  const handleUpdateImage = async (file) => {
    console.log("file-->", file);
    setFile(file);
    let formData = new FormData();
    formData.append("_id", updateCategoryId);
    formData.append("name", name);
    formData.append("file", file);

    try {
      const res = await axios.post(API_PATHS.INSERT_CATEGORY_IMAGE, formData);
      alert("Image uploaded successfully!");
    } catch (err) {
      alert("Image failed to upload");
    }
  };

  const getAllCategories = async () => {
    const res = await axios.get(API_PATHS.GET_ALL_CATEGORIES);
    const selectedCategory = res.data.result.find(
      (item) => item._id === updateCategoryId
    );
    setUpdateCategoryObj(selectedCategory);
    setName(selectedCategory ? selectedCategory.name : "");
  };

  useEffect(() => {
    getAllCategories();
  }, []);

  console.log("updateCategory-->", updateCategoryObj);

  return (
    <Box sx={{ width: "calc(100% - 230px)", m: 2 }}>
      {/* <Box
          sx={{
            flexDirection: "row-reverse",
            display: "flex",
          }}
        >
          <Button variant="contained">Add</Button>
        </Box> */}

      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Edit Category</Typography>
      </Box>
      <Paper
        elevation={3}
        sx={{
          paddingY: 2,
          paddingX: 4,
          boxShadow:
            "0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12)",
        }}
        variant="outlined"
      >
        {/* ------------------------------------------------------------------------------------------------ */}
        {/* Basic Layout */}
        {/* ------------------------------------------------------------------------------------------------ */}
        <Grid container>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-title" sx={{ mt: 0 }}>
              Name
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-name"
              placeholder="John Deo"
              value={name}
              onChange={(e) => setName(e.target.value)}
              fullWidth
            />
          </Grid>

          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-root">Product image</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            {updateCategoryObj && (
              <img
                src={`${API_URL}/${updateCategoryObj.master_folder_name}/${updateCategoryObj.file_name}`}
                style={{ objectFit: "contain", marginBottom: 5 }}
                width={200}
                height={200}
              />
            )}
            <FileUploader
              //   handleChange={handleChange}
              name="file"
              types={fileTypes}
              handleChange={handleUpdateImage}
            />
          </Grid>

          {/* <Grid item xs={12}>
            <Autocomplete
              multiple
              id="blog-tags"
              options={[]}
              value={selectedTags}
              freeSolo
              onChange={(e, value) => setSelectedTags(value)}
              renderInput={(params) => (
                <CustomTextField {...params} placeholder="Add your tags..." />
              )}
            />
          </Grid> */}

          <Grid item xs={12} mt={3}>
            <Button
              onClick={handleSubmit}
              variant="contained"
              sx={{ marginTop: 2, background: "#ed1b24", fontWeight: "600" }}

              //   onClick={handleSubmit}
            >
              Update
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  );
};
