import React, { useState } from "react";
import {
  Modal,
  Button,
  TextField,
  IconButton,
  Container,
  Box,
  Typography,
  FormControl,
  Select,
  MenuItem,
} from "@mui/material";
import { Close } from "@mui/icons-material";
import "./RewardModal.css";

const RewardModal = ({ open, handleClose, handleSubmit }) => {
  const [rewardAmount, setRewardAmount] = useState("");
  // const [remarks, setRemarks] = useState("");
  const [referReason, setReferReason] = useState("refer");
  const handleReferOptionChange = (event) => {
    setReferReason(event.target.value);
  };

  const handleInputChange = (event) => {
    setRewardAmount(event.target.value);
  };
  // const handleRemarkChange = (event) => {
  //   setRemarks(event.target.value);
  // };

  const handleFormSubmit = (event) => {
    event.preventDefault();
    handleSubmit(rewardAmount, referReason);
    setRewardAmount("");
  };

  return (
    <Modal open={open} onClose={handleClose}>
      <Container maxWidth="sm">
        <Box className="modal-container">
          <Box className="modal-header">
            <IconButton
              edge="end"
              color="inherit"
              onClick={handleClose}
              aria-label="close"
            >
              <Close />
            </IconButton>
          </Box>
          <Box className="modal-content">
            <Typography variant="h4" gutterBottom>
              Enter Reward Amount
            </Typography>
            <TextField
              label=""
              variant="outlined"
              fullWidth
              type="number"
              value={rewardAmount}
              onChange={handleInputChange}
              required
            />
            <FormControl
              sx={{
                border: "1px solid black",
                fontSize: "8px",
                width: "100%",
                mt: 1,
              }}
            >
              <Select
                label="Select Status"
                value={referReason}
                onChange={handleReferOptionChange}
              >
                <MenuItem value="refer">Refer</MenuItem>
                <MenuItem value="refund">Refund</MenuItem>
              </Select>
            </FormControl>
            <Button
              variant="contained"
              color="primary"
              onClick={handleFormSubmit}
              fullWidth
              className="rewardSubmitButton"
            >
              Submit
            </Button>
          </Box>
        </Box>
      </Container>
    </Modal>
  );
};

export default RewardModal;
