import {
  Box,
  Button,
  Card,
  CardContent,
  Checkbox,
  Container,
  FormControlLabel,
  Grid,
  Link,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";

import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import LoadingModal from "../Common/LoadingModal";
import axios from "axios";
import { api_base_url } from "../../utils/apiUtils";
import language from "../../utils/language";
import SvgIcons from "../Common/SvgIcons";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [rememberMe, setRememberMe] = useState(false);
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const [loginError, setLoginError] = useState(false);
  const navigateTo = useNavigate();

  useEffect(() => {
    const auth = JSON.parse(sessionStorage.getItem("user"));
    const token = auth ? auth.token : null;
    if (token) {
      navigateTo("/");
    }
  }, []);

  const handleRememberMeChange = (event) => {
    setRememberMe(event.target.checked);
  };

  const handleSnackbarClose = () => {
    setSnackbarOpen(false); // Close the Snackbar
    navigateTo("/");
  };

  const handleEmailChange = (event) => {
    setEmail(event.target.value);
  };

  const handleSubmit = async (event) => {
    setLoginError(false);
    event.preventDefault();
    if (email === "admin@admin.com" && password === "admin") {
      const user = {
        email: "admin@admin",
        token: "ey12345",
        role: "admin",
        userName: "Admin",
      };
      sessionStorage.setItem("user", JSON.stringify(user));
      setSnackbarMessage("Login Successfull");
      setSnackbarOpen(true);
    } else {
      setLoginError(true);
    }

    // try {
    //   let response = await axios.post(
    //     `${api_base_url}/login`,
    //     { email, password },
    //     {
    //       headers: {
    //         "Content-Type": "application/json",
    //       },
    //     }
    //   );

    //   if (response.data.statusCode === 200) {
    //     console.log("User Details-->", response.data);
    //     const user = { ...response.data.data, token: response.data.token };
    //     sessionStorage.setItem("user", JSON.stringify(user));
    //     setSnackbarMessage("Login Successfull");
    //     setSnackbarOpen(true);
    //   } else if (response.data.statusCode === 400 || 500) {
    //     setLoginError(true);
    //     console.log("Error In Login-->", response.data.error);
    //   }
    // } catch (err) {
    //   console.log("Error In Loginnn-->", err);
    // }
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };
  return (
    <div style={{ background: "#ed1b24", height: "100vh" }}>
      <Container maxWidth="xs">
        <Snackbar
          open={snackbarOpen}
          autoHideDuration={500} // Adjust the duration as needed
          onClose={handleSnackbarClose}
          message={snackbarMessage}
          anchorOrigin={{ vertical: "top", horizontal: "right" }}
          style={{ backgroundColor: "green" }}
          color="green"
        />
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Card sx={{ padding: 2, marginTop: 22 }}>
            <CardContent>
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                {/* <img
                  src={"logo.png"}
                  alt={"logo"}
                  style={{
                    width: "30px",
                    background: "#fafafa",
                    borderRadius: "15px",
                    marginRight: "5px",
                  }}
                /> */}

                {/* <SvgIcons icon="icon" /> */}
                <img
                  src="https://www.luxorpen.com/images/logo/logo.png"
                  width={150}
                />
                <Typography component="h1" variant="h5">
                  Admin
                </Typography>
              </Box>
              <Box component="form" onSubmit={handleSubmit}>
                <TextField
                  value={email}
                  label="Email"
                  type="email"
                  fullWidth
                  margin="normal"
                  required
                  onChange={handleEmailChange}
                />
                <TextField
                  value={password}
                  label="Password"
                  required
                  fullWidth
                  type="password"
                  margin="normal"
                  onChange={handlePasswordChange}
                />
                {loginError && (
                  <Typography color="red" className="ahashakeheartache">
                    Invalid Credentials
                  </Typography>
                )}
                <FormControlLabel
                  control={
                    <Checkbox
                      checked={rememberMe}
                      onChange={handleRememberMeChange}
                      color="primary"
                    />
                  }
                  label="Remember Me"
                />

                <Button
                  type="submit"
                  variant="contained"
                  fullWidth
                  sx={{ my: 2, backgroundColor: "#ed1b24" }}
                >
                  Login
                </Button>
                <Grid container>
                  <Grid item xs>
                    <Link href="#" variant="body2">
                      Forgot Password
                    </Link>
                  </Grid>
                  {/* <Grid item>
                    <Link href="#" variant="body2">
                      {language.dontHaveAccount}
                    </Link>
                  </Grid> */}
                </Grid>
              </Box>
            </CardContent>
          </Card>
        </Box>
      </Container>
    </div>
  );
};

export default Login;
