import {
  Box,
  Button,
  Grid,
  MenuItem,
  Paper,
  Select,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import React, { useEffect, useRef, useState } from "react";
import CustomFormLabel from "./Common/CustomFormLabel";
import CustomTextField from "./Common/CustomTextField";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import "./Quill.css";
import axios from "axios";
import { API_PATHS } from "../utils/apiUtils";
import DragDrop from "./Common/DragDrop";
import { FileUploader } from "react-drag-drop-files";

export const AddSubCategory = () => {
  const fileTypes = ["JPG", "PNG", "GIF"];

  const nameRef = useRef();
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const colorRef = useRef();

  const [subCategories, setSubCategories] = useState([]);
  const [selectedSubCategory, setSelectedSubCategory] = useState();

  const getAllCategories = async () => {
    const response = await axios.get(API_PATHS.GET_ALL_CATEGORIES);
    const categoriesData = response.data.result.map((item) => ({
      id: item._id,
      name: item.name,
      master_folder_name: item.master_folder_name,
    }));

    setCategories(categoriesData);
  };
  console.log("selectedCategory-->", selectedCategory);

  useEffect(() => {
    getAllCategories();
  }, []);

  const handleChange = async (file) => {
    // setFile(file);
    // let formData = new FormData();
    // formData.append("file", file);
    // formData.append("product_id", props.product_id);
    // formData.append("_cat_id", props._cat_id);
    // try {
    //   console.log("formData-->", formData.get("_cat_id"));
    //   const res = await axios.post(
    //     API_PATHS.INSERT_CAT_PRODUCTS_FOR_PEN,
    //     formData
    //   );
    //   alert("Image uploaded successfully!");
    // } catch (err) {
    //   console.log(err.message);
    // }
  };

  const handleChangeCategory = async (e) => {
    setSelectedCategory(e.target.value);
    const subCategoriesData = await axios.post(
      API_PATHS.GET_PEN_SUB_CATEGORIES,
      { category_id: e.target.value }
    );

    console.log("subCategory-->", subCategoriesData);

    const temp = subCategoriesData.data.result.map((item) => ({
      id: item._id,
      category: item.category,
    }));

    setSubCategories(temp);
  };

  const handleSubmit = async () => {
    const selectedCatName = categories.find(
      (item) => item.id == selectedCategory
    );
    // console.log("selectedCatName-->", selectedCatName.name);

    const categoryNameRemovingSpaces = nameRef.current.value
      .split(" ")
      .join("_");

    const data = {
      category: nameRef.current.value,
      product_category_type: selectedCategory,
      color: colorRef.current.value,
      master_folder_name: `${
        selectedCatName.master_folder_name
      }/${categoryNameRemovingSpaces.toLowerCase()}`,
      product_folder_name: `${
        selectedCatName.master_folder_name
      }/${categoryNameRemovingSpaces.toLowerCase()}/product_images`,
    };

    console.log("add sub-category data--->", data);

    try {
      const res = await axios.post(API_PATHS.ADD_SUB_CATEGORY, data);
      alert("Sub-category added successfully!");
    } catch (err) {
      alert("Error, failed to add sub-category");
    }
  };

  return (
    <Box sx={{ width: "calc(100% - 230px)", m: 2 }}>
      {/* <Box
          sx={{
            flexDirection: "row-reverse",
            display: "flex",
          }}
        >
          <Button variant="contained">Add</Button>
        </Box> */}

      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Add Sub-category</Typography>
      </Box>
      <Paper
        elevation={3}
        sx={{
          paddingY: 2,
          paddingX: 4,
          boxShadow:
            "0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12)",
        }}
        variant="outlined"
      >
        {/* ------------------------------------------------------------------------------------------------ */}
        {/* Basic Layout */}
        {/* ------------------------------------------------------------------------------------------------ */}
        <Grid container>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-title" sx={{ mt: 0 }}>
              Sub category name
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-name"
              placeholder="John Deo"
              inputRef={nameRef}
              fullWidth
            />
          </Grid>

          <Grid item xs={12} alignItems="center">
            <CustomFormLabel htmlFor="demo-simple-select">
              Category type
            </CustomFormLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={selectedCategory}
              onChange={(e) => handleChangeCategory(e)}
              color="error"
              fullWidth
              size="small"
            >
              {categories.map((item) => (
                <MenuItem key={item.id} value={item.id}>
                  {item.name}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-color">Color</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-color"
              placeholder="color"
              fullWidth
              inputRef={colorRef}
            />
          </Grid>

          {/* 4 */}
          {/* <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-phone">Description</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              //   value={blogDate}
              type="date"
              id="fs-date"
              placeholder="John Deo"
              fullWidth
            />
          </Grid> */}

          {/* <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-root">Product image</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <FileUploader
              handleChange={handleChange}
              name="file"
              types={fileTypes}
            />
          </Grid> */}

          {/* <Grid item xs={12}>
            <Autocomplete
              multiple
              id="blog-tags"
              options={[]}
              value={selectedTags}
              freeSolo
              onChange={(e, value) => setSelectedTags(value)}
              renderInput={(params) => (
                <CustomTextField {...params} placeholder="Add your tags..." />
              )}
            />
          </Grid> */}

          <Grid item xs={12} mt={3}>
            <Button
              variant="contained"
              color="primary"
              sx={{ fontWeight: "600", background: "#ed1b24" }}
              onClick={handleSubmit}
            >
              Submit
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  );
};
