import React from "react";
import { Box, Card, Typography } from "@mui/material";
import { EmailOutlined, PlaceOutlined } from "@mui/icons-material";
import { useTheme } from "@mui/material/styles";
import AccountBalanceWalletIcon from "@mui/icons-material/AccountBalanceWallet";

function SecondaryHeader({
  name,
  email,
  phone,
  amount,
  address,
  bankAccountHolderName,
  bankAccountNumber,
  bankName,
  gstNumber,
  ifscCode,
  pan,
}) {
  const theme = useTheme();

  return (
    <Card sx={{ display: "flex", alignItems: "center", p: 2 }}>
      <img
        src={`https://ui-avatars.com/api/?background=007bff&color=fff&name=${name}`}
        alt="Application Logo"
        style={{ width: "7rem" }}
      />
      <Box sx={{ ml: 2, display: "flex" }}>
        <Box>
          <Typography variant="h3">{name}</Typography>
          <Box flexDirection={"row"} display={"flex"}>
            <EmailOutlined color="primary" />
            <Typography sx={{ ml: 1 }}>{email}</Typography>
          </Box>
          <Box flexDirection={"row"} display={"flex"}>
            <PlaceOutlined color="primary" />
            <Typography sx={{ ml: 1 }}>{phone}</Typography>
          </Box>
          <Box flexDirection={"row"} display={"flex"}>
            <AccountBalanceWalletIcon color="primary" />
            <Typography sx={{ ml: 1 }}> {amount}</Typography>
          </Box>
          <Box flexDirection={"row"} display={"flex"}>
            <PlaceOutlined color="primary" />
            <Typography sx={{ ml: 1 }}>{address}</Typography>
          </Box>
        </Box>
        <Box sx={{ ml: 1, alignItems: "" }}>
          <Box flexDirection={"row"} display={"flex"}>
            <Typography sx={{ ml: 1 }}>
              Bank Account Holder: {bankAccountHolderName}
            </Typography>
          </Box>
          <Box flexDirection={"row"} display={"flex"}>
            <Typography sx={{ ml: 1 }}>
              Bank Account Number: {bankAccountNumber}
            </Typography>
          </Box>
          <Box flexDirection={"row"} display={"flex"}>
            <Typography sx={{ ml: 1 }}>Bank Name: {bankName}</Typography>
          </Box>
          <Box flexDirection={"row"} display={"flex"}>
            <Typography sx={{ ml: 1 }}>GST Number: {gstNumber}</Typography>
          </Box>
          <Box flexDirection={"row"} display={"flex"}>
            <Typography sx={{ ml: 1 }}>IFSC Code: {ifscCode}</Typography>
          </Box>
          <Box flexDirection={"row"} display={"flex"}>
            <Typography sx={{ ml: 1 }}>PAN: {pan}</Typography>
          </Box>
        </Box>
      </Box>
    </Card>
  );
}

export default SecondaryHeader;
