import {
  Alert,
  Box,
  Button,
  ButtonGroup,
  Grid,
  Modal,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { DataGrid } from "@mui/x-data-grid";
import DoneIcon from "@mui/icons-material/Done";
import { Clear } from "@mui/icons-material";
import RewardModal from "./Common/RewardModal";
import RejectReferralModal from "./Common/RejectReferralModal";
import { API_PATHS, API_URL } from "../utils/apiUtils";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import date from "date-and-time";
import axios from "axios";
import DownloadIcon from "@mui/icons-material/Download";
import CareerCSVModal from "./Common/CareerCSVModal";

const Careers = () => {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 400,
    height: "60vh",
    // overflowY: "scroll",
    bgcolor: "background.paper",
    // border: "1px solid #000",
    boxShadow: 24,
    borderRadius: "7px",
    p: 4,
  };

  const [isModalOpen, setModalOpen] = useState(false);
  const [isModalOpen1, setModalOpen1] = useState(false);
  const [referralRow, setReferralRow] = useState([]);
  const [amount, setAmount] = useState();
  const [selectedRowId, setSelectedRowId] = useState();
  const [referId, setReferId] = useState();
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const [suggestedRow, setSuggestedRow] = useState([]);
  const [textModal, setTextModal] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [totalDataCount, setTotalDataCount] = useState(0);
  const [loading, setLoading] = useState(false);
  const [isChecked, setIsChecked] = useState(false);
  const [CSVModalOpen, setCSVModalOpen] = useState(false);
  const [csvSnackbar, setCSVSnackbar] = useState(false);

  const handleOpenModal = () => {
    setCSVModalOpen(true);
  };

  const handleCloseModal = () => {
    setCSVModalOpen(false);
  };

  const closeTextModal = () => setTextModal("");
  const openTextModal = ({ row }) => setTextModal(row);

  console.log("suggested row--->", suggestedRow);
  const [referReason, setReferReason] = useState("Refer");

  const [selectedOption, setSelectedOption] = useState("Pending"); // Default selected option

  const handleOptionChange = async (event, databaseId) => {
    const optionValue = event.target.value;
    console.log("Datbase ID -->", databaseId, optionValue);
    try {
      console.log("Url Path-->", API_PATHS.CHANGE_REFERRAL_STATUS);
      const dataObject = {
        id: databaseId,
        status: optionValue,
      };
      console.log(dataObject);
      const response = await axios.post(
        API_PATHS.CHANGE_REFERRAL_STATUS,
        dataObject
      );
      if (response.status === 200) {
        console.log("Success");
        const updatedArr = referralRow.map((item) => {
          if (item.databaseId === databaseId) {
            return { ...item, referralStatus: optionValue };
          } else {
            return item;
          }
        });
        setReferralRow(updatedArr);
        setSnackbarMessage("Sucess");
        setSnackbarOpen(true);
      }
    } catch (error) {
      console.log("Error In Status Change-->", error);
      setSnackbarMessage("Failed");
      setSnackbarOpen(true);
    }
    // setSelectedOption(optionValue);
  };

  const handleDownloadCSVData = async () => {
    const res = await axios.get(`${API_PATHS.DOWNLOAD_CAREER_CSV}`);
    setCSVSnackbar(res.data.message);
  };

  const handleOnBlur = async (event, row) => {
    const amountValue = event.target.value;
    if (amountValue) {
      try {
        const dataObject = {
          member_id: row.referredBy,
          referred_by_name: row.name,
          amount: amountValue,
          remark: "Refer",
          status: "success",
          refferral_id: row.databaseId,
          referred_to_name: row.name,
          referred_to_email: row.email,
          referred_to_mobile: row.mobileNumber,
          property_name: row.propertyName,
          property_location: row.propertyLocation,
          property_link: row.propertyLink,
        };
        const response = await axios.post(
          API_PATHS.ADD_TRANSACTION,
          dataObject
        );
        if (response.status === 200) {
          console.log("Success");
          event.target.value = "";
          setSnackbarMessage("Amount Successfully Added");
          setSnackbarOpen(true);
        }
      } catch (error) {
        console.log("Error In Reward-->", error);
      }
    }
  };

  const handleReferOptionChange = (event) => {
    setReferReason();
  };

  useEffect(() => {
    // getAllReferrals();
    getSuggestedProperties();
  }, [currentPage]);

  const handlePageChange = (params) => {
    setCurrentPage(params.page + 1); // Adding 1 because the page is zero-based
  };

  const openModal1 = () => {
    setModalOpen1(true);
  };

  const closeModal1 = () => {
    setModalOpen1(false);
  };

  const handleConfirmYes = () => {
    // Handle the "Yes" button click here.
    // You can add your logic for declining the referral.
    // For example, you can make an API request to decline the referral.
    console.log("Declining referral...");
    closeModal1(); // Close the modal after confirming.
  };

  const openModal = () => {
    setModalOpen(true);
  };

  const closeModal = () => {
    setModalOpen(false);
  };

  const handleSubmit = async (rewardAmount, refer) => {
    setAmount(rewardAmount);
    // Handle the form submission here, e.g., send the rewardAmount to an API.
    console.log("Reward Amount submitted:", rewardAmount);

    try {
      console.log("Url Path-->", API_PATHS.ADD_TRANSACTION);
      const dataObject = {
        member_id: referId,
        amount: rewardAmount,
        refferral_id: selectedRowId,
        status: "success",
        remark: refer,
      };
      console.log(dataObject);
      const response = await axios.post(API_PATHS.ADD_TRANSACTION, dataObject);
      if (response.status === 200) {
        console.log("Success");
        setSnackbarMessage("Amount Successfully Added");
        setSnackbarOpen(true);
        closeModal();
      }
    } catch (error) {
      console.log("Error In Reward-->", error);
      closeModal();
    }
  };

  const handleSnackbarClose = () => {
    setSnackbarOpen(false); // Close the Snackbar
  };

  const exportCSV = async (rows, columns) => {
    const csvRows = [];

    // Create the header row
    const exportedColumns = columns.filter((item) => !item.hideInExport);
    const header = exportedColumns.map((column) => column.headerName);
    csvRows.push(header.join(","));

    // Create the data rows
    rows.forEach((row) => {
      const rowData = exportedColumns.map((column) => {
        return row[column.field];
      });
      csvRows.push(rowData.join(","));
    });

    // Combine rows into a single CSV string
    const csvString = csvRows.join("\n");

    // Create a blob containing the CSV data
    const blob = new Blob([csvString], { type: "text/csv" });

    // Create a temporary URL to the blob
    const url = window.URL.createObjectURL(blob);

    // Create a link element to trigger the download
    const a = document.createElement("a");
    a.href = url;
    a.download = "career_list.csv";

    // Trigger the click event on the link to initiate the download
    a.click();

    // Clean up the temporary URL
    window.URL.revokeObjectURL(url);
  };

  const getSuggestedProperties = async () => {
    try {
      setLoading(true);
      const response = await axios.get(
        `${API_PATHS.GET_CAREER_LIST}/${10}/${currentPage}`
      );
      console.log("suggested-properties--->", response.data.data);

      const resArr = response.data.data.map((item, index) => {
        return {
          sno: index + 1,
          databaseId: item._id,
          id: `CAR${item._id.slice(-5)}`,
          name: item.name,
          mobileNumber: item.contactNumber,
          email: item.email,
          profile: item.profile,
          resume_folder_name: item.resume_folder_name,
          file_name: item.file_name,
          link: API_URL + "/" + item.resume_folder_name + item.file_name,
          created_on: date.format(
            new Date(item.created_on),
            "ddd, MMM DD YYYY hh:mm A"
          ),
        };
      });
      setLoading(false);
      setSuggestedRow(resArr);
      setTotalDataCount(response.data.total_data_count);
    } catch (err) {
      setLoading(false);
      console.log("Error In get all suggested properties-->", err);
    }
  };

  const columns = [
    { field: "sno", headerName: "S.No", width: 70 },
    { field: "id", headerName: "ID", width: 100 },
    // {
    //   field: "referralStatus",
    //   headerName: "Status",
    //   width: 200,
    //   renderCell: ({ row }) => {
    //     return (
    //       <FormControl
    //         sx={{ border: "1px solid black", fontSize: "8px", width: "200px" }}
    //       >
    //         <Select
    //           label="Select Status"
    //           value={row.referralStatus}
    //           onChange={(event) => handleOptionChange(event, row.databaseId)}
    //         >
    //           <MenuItem value="Enquiry Received">Enquiry Received</MenuItem>
    //           <MenuItem value="Site Visited">Site Visited</MenuItem>
    //           <MenuItem value="Booked">Booked</MenuItem>
    //           <MenuItem value="Not Booked">Not Booked</MenuItem>
    //         </Select>
    //       </FormControl>
    //     );
    //   },
    // },
    // {
    //   field: "amount",
    //   headerName: "Amount",
    //   width: 150,
    //   sortable: true,
    //   renderCell: ({ row }) => {
    //     return (
    //       <div
    //         style={{
    //           display: "flex",
    //           justifyContent: "space-between",
    //           width: "100%",
    //           alignItems: "center",
    //         }}
    //       >
    //         <TextField
    //           label=""
    //           variant="outlined"
    //           fullWidth
    //           type="number"
    //           onClick={(event) => event.stopPropagation()}
    //           disabled={row.referralStatus !== "Booked"}
    //           InputProps={{ style: { height: "40px" } }}
    //           onBlur={(event) => handleOnBlur(event, row)}
    //         />
    //         {/* <DoneIcon
    //           style={{ color: "#50C878", cursor: "pointer" }}
    //           onClick={(event) =>
    //             handleDoneClick(row.databaseId, event, row.referredBy)
    //           }
    //         />
    //         <Clear
    //           style={{ color: "red", cursor: "pointer" }}
    //           onClick={(event) =>
    //             handleCrossClick(row.databaseId, event, row.referred_by)
    //           }
    //         /> */}
    //       </div>
    //     );
    //   },
    // },
    { field: "name", headerName: "Name", width: 150 },
    { field: "email", headerName: "Email", width: 200 },
    { field: "mobileNumber", headerName: "Mobile No.", width: 130 },
    { field: "profile", headerName: "Resume", width: 170 },
    {
      field: "file_name",
      headerName: "File name",
      width: 250,
      renderCell: ({ row }) => {
        return (
          <a href={row.link} target="_blank">
            {row.file_name}
          </a>
        );
      },
    },
    { field: "created_on", headerName: "Submitted on", width: 230 },
    // {
    //   field: "propertyName",
    //   headerName: "Property Name",
    //   width: 230,

    //   renderCell: ({ row }) => {
    //     return (
    //       <p
    //         style={{
    //           textDecoration: "none",
    //         }}
    //         target="_blank"
    //         onClick={(e) => {
    //           e.stopPropagation();
    //         }}
    //       >
    //         {row.propertyName}, {row.propertyLocation}
    //       </p>
    //     );
    //   },
    // },
    // { field: "propertyType", headerName: "Property type", width: 130 },
    // { field: "requirement", headerName: "Requirement", width: 130 },
    // { field: "referType", headerName: "Sale/Purchase/Refer", width: 150 },
    // { field: "budget", headerName: "Budget", width: 130 },
    // { field: "description", headerName: "Description", width: 230 },

    // { field: "propertyLocation", headerName: "Location", width: 300 },

    //   {
    //     field: "fullName",
    //     headerName: "Full name",
    //     description: "This column has a value getter and is not sortable.",
    //     sortable: false,
    //     width: 160,
    //     valueGetter: (params) =>
    //       `${params.row.firstName || ""} ${params.row.lastName || ""}`,
    //   },
  ];

  // columns.unshift({
  //   field: "action",
  //   headerName: "Actions",
  //   width: 80,
  //   sortable: true,
  //   renderCell: ({ row }) => {
  //     return (
  //       <div
  //         style={{
  //           display: "flex",
  //           justifyContent: "space-between",
  //           width: "100%",
  //         }}
  //       >
  //         <DoneIcon
  //           style={{ color: "#50C878", cursor: "pointer" }}
  //           onClick={(event) =>
  //             handleDoneClick(row.databaseId, event, row.referredBy)
  //           }
  //         />
  //         <Clear
  //           style={{ color: "red", cursor: "pointer" }}
  //           onClick={(event) =>
  //             handleCrossClick(row.databaseId, event, row.referred_by)
  //           }
  //         />
  //       </div>
  //     );
  //   },
  // });

  return (
    <Box sx={{ width: "calc(100% - 230px)", m: 2 }}>
      {/* <Box
        sx={{
          flexDirection: "row-reverse",
          display: "flex",
        }}
      >
        <Button variant="contained">Add</Button>
      </Box> */}

      <Snackbar
        open={snackbarOpen}
        autoHideDuration={1000} // Adjust the duration as needed
        onClose={handleSnackbarClose}
        message={snackbarMessage}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      />
      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Career List</Typography>
        <Box
          sx={{
            flexDirection: "row",
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              flexDirection: "row",
              display: "flex",
              alignItems: "center",
              marginRight: 6,
            }}
          >
            {/* <Typography fontWeight={600}>Export all data</Typography>
            <input
              type="checkbox"
              className="toggle-export"
              onChange={(e) => setIsChecked(e.target.checked)}
            /> */}
          </Box>

          <>
            <Button
              variant="contained"
              onClick={() => exportCSV(suggestedRow, columns)}
              style={{
                height: "80%",
                backgroundColor: "#ed1b24",
                marginRight: 20,
                marginLeft: 140,
              }}
            >
              Export To CSV
            </Button>
          </>

          <ButtonGroup variant="contained">
            <Button
              variant="contained"
              onClick={handleDownloadCSVData}
              style={{
                height: "80%",
                backgroundColor: "#ed1b24",
                borderColor: "white",
              }}
            >
              Generate All
            </Button>
            <Button
              onClick={handleOpenModal}
              style={{
                height: "80%",
                backgroundColor: "#ed1b24",
                outline: "none",
              }}
            >
              <DownloadIcon />
            </Button>
          </ButtonGroup>
        </Box>
      </Box>
      <CareerCSVModal open={CSVModalOpen} handleClose={handleCloseModal} />

      <Box sx={{ width: "100%" }}>
        <DataGrid
          autoHeight
          rows={suggestedRow}
          columns={columns}
          loading={loading}
          rowCount={totalDataCount}
          onPaginationModelChange={handlePageChange}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 10 },
            },
          }}
          // pageSizeOptions={[10, 10]}
          onCellClick={(params) => {
            if (params.field === "__check__") {
              return;
            }
            openTextModal(params);
          }}
          checkboxSelection
        />
      </Box>
      {/* <Modal open={textModal} onClose={closeTextModal}>
        <Box sx={style}>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            Name: {textModal.name}
          </Typography>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            Email: {textModal.email}
          </Typography>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            Company Name: {textModal.companyName}
          </Typography>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            Mobile: {textModal.mobileNumber}
          </Typography>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            Requirements : {textModal.requirements}
          </Typography>
        </Box>
      </Modal> */}
      <Snackbar
        open={csvSnackbar}
        autoHideDuration={6000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() => setCSVSnackbar(false)}
      >
        <Alert
          onClose={() => setCSVSnackbar(false)}
          severity="success"
          sx={{ width: "100%" }}
        >
          {csvSnackbar}
        </Alert>
      </Snackbar>
      <RewardModal
        open={isModalOpen}
        handleClose={closeModal}
        handleSubmit={handleSubmit}
      />
      <RejectReferralModal
        open={isModalOpen1}
        handleClose={closeModal1}
        handleConfirm={handleConfirmYes}
      />
    </Box>
  );
};

export default Careers;
