import {
  Box,
  Button,
  Grid,
  Snackbar,
  Stack,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { DataGrid } from "@mui/x-data-grid";
import DoneIcon from "@mui/icons-material/Done";
import { Clear } from "@mui/icons-material";
import RewardModal from "./Common/RewardModal";
import RejectReferralModal from "./Common/RejectReferralModal";
import { API_PATHS } from "../utils/apiUtils";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import axios from "axios";
import { useLocation, useNavigate } from "react-router-dom";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import SecondaryHeader from "./Common/SecondaryHeader";

const UserDetail = () => {
  const [selectedRowId, setSelectedRowId] = useState();
  const [referId, setReferId] = useState();
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const location = useLocation();
  const [referralRow, setReferralRow] = useState([]);
  const [referralEarnings, setReferralEarnings] = useState();
  const [interestedRow, setInterestedRow] = useState([]);
  const [showReferral, setShowReferral] = useState(true);

  const { userDetails } = location.state;

  console.log("User Details -- >", userDetails);

  const navigateTo = useNavigate();

  useEffect(() => {
    getAllReferrals();
    getReferralEarnings();
  }, []);

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  const handleOptionChange = async (event, databaseId) => {
    const optionValue = event.target.value;
    console.log("Datbase ID -->", databaseId, optionValue);
    try {
      console.log("Url Path-->", API_PATHS.CHANGE_REFERRAL_STATUS);
      const dataObject = {
        id: databaseId,
        status: optionValue,
      };
      console.log(dataObject);
      const response = await axios.post(
        API_PATHS.CHANGE_REFERRAL_STATUS,
        dataObject
      );
      if (response.status === 200) {
        console.log("Success");
        if (showReferral) {
          const updatedArr = referralRow.map((item) => {
            if (item.databaseId === databaseId) {
              return { ...item, referralStatus: optionValue };
            } else {
              return item;
            }
          });
          setReferralRow(updatedArr);
        } else {
          const updatedArr = interestedRow.map((item) => {
            if (item.databaseId === databaseId) {
              return { ...item, referralStatus: optionValue };
            } else {
              return item;
            }
          });
          setInterestedRow(updatedArr);
        }

        setSnackbarMessage("Sucess");
        setSnackbarOpen(true);
      }
    } catch (error) {
      console.log("Error In Status Change-->", error);
      setSnackbarMessage("Failed");
      setSnackbarOpen(true);
    }
    // setSelectedOption(optionValue);
  };

  const handleOnBlur = async (event, row) => {
    const amountValue = event.target.value;
    if (amountValue) {
      try {
        const dataObject = {
          member_id: row.referredBy,
          referred_by_name: showReferral ? row.referred_by_name : row.name,
          amount: amountValue,
          remark: "Refer",
          status: "success",
          refferral_id: row.databaseId,
          referred_to_name: row.name,
          referred_to_email: row.email,
          referred_to_mobile: row.mobileNumber,
          property_name: row.propertyName,
          property_location: row.propertyLocation,
          property_link: row.propertyLink,
        };
        const response = await axios.post(
          API_PATHS.ADD_TRANSACTION,
          dataObject
        );
        if (response.status === 200) {
          console.log("Success");
          event.target.value = "";
          setSnackbarMessage("Amount Successfully Added");
          setSnackbarOpen(true);
        }
      } catch (error) {
        console.log("Error In Reward-->", error);
      }
    }
  };

  const getAllReferrals = async () => {
    try {
      const response = await axios.post(API_PATHS.GET_REFERRALS_BY_USERID, {
        userId: userDetails.id,
      });
      const referralFiltered = response.data.filter(
        (item) => item.interested_user !== item.referred_by
      );

      const interestedPropertyFiltered = response.data.filter(
        (item) => item.interested_user === item.referred_by
      );
      console.log("Data Interested--->", interestedPropertyFiltered);
      const resArrInterested = interestedPropertyFiltered.map((item, index) => {
        return {
          sno: index + 1,
          databaseId: item._id,
          id: `BKL${item._id.slice(-5)}`,
          propertyName: item.propertyName,
          propertyLocation: item.propertyLocation,
          name: item.name,
          mobileNumber: item.mobile,
          email: item.email,
          referralStatus: item.referralStatus,
          propertyLink: item.propertyLink,
          referredBy: item.referred_by,
          referred_on: item.created_date,
        };
      });

      setInterestedRow(resArrInterested);
      const resArr = referralFiltered.map((item, index) => {
        return {
          sno: index + 1,
          databaseId: item._id,
          id: `RF${item._id.slice(-5)}`,
          propertyName: item.propertyName,
          propertyLocation: item.propertyLocation,
          name: item.name,
          mobileNumber: item.mobile,
          email: item.email,
          referralStatus: item.referralStatus,
          propertyLink: item.propertyLink,
          referredBy: item.referred_by,
          referred_by_name: item.referred_by_name,
          referred_on: item.created_date,
        };
      });
      console.log("dfsfsdf", resArr);
      setReferralRow(resArr);
    } catch (error) {
      console.log("Error In get all referrals-->", error);
    }
  };

  const getReferralEarnings = async () => {
    try {
      const response = await axios.post(
        API_PATHS.GET_REFERRAL_TRANSACTIONS_BY_USER_ID,
        {
          userId: userDetails.id,
        }
      );

      const transactionArr = response.data.userTransactions;

      const amount = transactionArr.reduce(
        (accumulator, item) => accumulator + Number(item.amount),
        0
      );

      console.log("Referral Earnings -->", amount);

      setReferralEarnings(amount);
    } catch (error) {
      console.log("Error In get all referrals-->", error);
    }
  };

  const referralsColumns = [
    { field: "sno", headerName: "S.No", width: 70 },
    { field: "id", headerName: "ID", width: 100 },
    {
      field: "referralStatus",
      headerName: "Status",
      width: 200,
      renderCell: ({ row }) => {
        return (
          <FormControl
            sx={{ border: "1px solid black", fontSize: "8px", width: "200px" }}
          >
            {showReferral ? (
              <Select
                label="Select Status"
                value={row.referralStatus}
                onChange={(event) => handleOptionChange(event, row.databaseId)}
              >
                <MenuItem value="Referral Received">Referral Received</MenuItem>
                <MenuItem value="Contacted">Contacted</MenuItem>
                <MenuItem value="Converted">Converted</MenuItem>
                <MenuItem value="Not Booked">Not Booked</MenuItem>
              </Select>
            ) : (
              <Select
                label="Select Status"
                value={row.referralStatus}
                onChange={(event) => handleOptionChange(event, row.databaseId)}
              >
                <MenuItem value="Enquiry Received">Enquiry Received</MenuItem>
                <MenuItem value="Site Visited">Site Visited</MenuItem>
                <MenuItem value="Booked">Booked</MenuItem>
                <MenuItem value="Not Booked">Not Booked</MenuItem>
              </Select>
            )}
          </FormControl>
        );
      },
    },
    {
      field: "amount",
      headerName: "Amount",
      width: 150,
      sortable: true,
      renderCell: ({ row }) => {
        return (
          <div
            style={{
              display: "flex",
              justifyContent: "space-between",
              width: "100%",
              alignItems: "center",
            }}
          >
            <TextField
              label=""
              variant="outlined"
              fullWidth
              type="number"
              onClick={(event) => event.stopPropagation()}
              disabled={
                showReferral
                  ? row.referralStatus !== "Converted"
                  : row.referralStatus !== "Booked"
              }
              InputProps={{ style: { height: "40px" } }}
              onBlur={(event) => handleOnBlur(event, row)}
            />
            {/* <DoneIcon
              style={{ color: "#50C878", cursor: "pointer" }}
              onClick={(event) =>
                handleDoneClick(row.databaseId, event, row.referredBy)
              }
            />
            <Clear
              style={{ color: "red", cursor: "pointer" }}
              onClick={(event) =>
                handleCrossClick(row.databaseId, event, row.referred_by)
              }
            /> */}
          </div>
        );
      },
    },
    { field: "name", headerName: "Name", width: 150 },
    { field: "email", headerName: "Email", width: 200 },
    { field: "mobileNumber", headerName: "Mobile No.", width: 130 },
    {
      field: "propertyName",
      headerName: "Property Name",
      flex: 1,
      renderCell: ({ row }) => {
        return (
          <a
            href={row.propertyLink || "http://google.com"}
            style={{ textDecoration: "none" }}
            target="_blank"
            onClick={(e) => e.stopPropagation()}
          >
            {row.propertyName}, {row.propertyLocation}
          </a>
        );
      },
    },

    {
      field: "referred_on",
      headerName: "Referred On",
      width: 150,
      sortable: true,
      renderCell: ({ row }) => {
        const timestamp = row.referred_on;
        const dateObj = new Date(timestamp);

        const day = String(dateObj.getDate()).padStart(2, "0");
        const month = String(dateObj.getMonth() + 1).padStart(2, "0"); // Months are zero-based
        const year = dateObj.getFullYear();
        const hours = String(dateObj.getHours()).padStart(2, "0");
        const minutes = String(dateObj.getMinutes()).padStart(2, "0");

        const formattedDate = `${day}-${month}-${year}, ${hours}:${minutes}`;

        return <div>{formattedDate}</div>;
      },
    },
  ];

  return (
    <Box sx={{ width: "85%", m: 2 }}>
      {/* <Box
          sx={{
            flexDirection: "row-reverse",
            display: "flex",
          }}
        >
          <Button variant="contained">Add</Button>
        </Box> */}

      <Snackbar
        open={snackbarOpen}
        autoHideDuration={1000}
        onClose={handleSnackbarClose}
        message={snackbarMessage}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      />

      <SecondaryHeader
        name={userDetails.fullName}
        phone={userDetails.mobile}
        email={userDetails.email}
        amount={referralEarnings}
        address={userDetails.address}
        bankAccountHolderName={userDetails.bank_account_holder_name}
        bankAccountNumber={userDetails.bank_account_number}
        bankName={userDetails.bank_name}
        gstNumber={userDetails.gst_number}
        ifscCode={userDetails.ifsc_code}
        pan={userDetails.pan}
      />

      <Box sx={{ height: 500, width: "100%", mt: 2 }}>
        <Stack spacing={2} direction="row">
          <Button
            variant={showReferral ? "contained" : "outlined"}
            onClick={() => setShowReferral(true)}
          >
            Referrals
          </Button>
          <Button
            variant={showReferral ? "outlined" : "contained"}
            onClick={() => setShowReferral(false)}
          >
            Interested
          </Button>
        </Stack>
        <DataGrid
          sx={{ overflowX: "scroll", width: "100%", mt: 2 }}
          autoHeight
          rows={showReferral ? referralRow : interestedRow}
          columns={referralsColumns}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 10 },
            },
          }}
          pageSizeOptions={[10, 10]}
          checkboxSelection
        />
      </Box>
    </Box>
  );
};

export default UserDetail;
