import React from "react";
import {
  Modal,
  Button,
  IconButton,
  Typography,
  Box,
  Container,
} from "@mui/material";
import { Close } from "@mui/icons-material";

const RejectReferralModal = ({ open, handleClose, handleConfirm }) => {
  const handleNoClick = () => {
    handleClose();
  };

  return (
    <Modal open={open} onClose={handleClose}>
      <Container maxWidth="sm">
        <Box className="modal-container">
          <Box className="modal-header">
            <IconButton
              edge="end"
              color="inherit"
              onClick={handleClose}
              aria-label="close"
            >
              <Close />
            </IconButton>
          </Box>
          <Box className="modal-content">
            <Typography variant="h5" gutterBottom>
              Are you sure you want to decline this referral?
            </Typography>
            <Box className="modal-buttons">
              <Button
                variant="contained"
                color="secondary"
                onClick={handleConfirm}
              >
                Yes
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={handleNoClick}
              >
                No
              </Button>
            </Box>
          </Box>
        </Box>
      </Container>
    </Modal>
  );
};

export default RejectReferralModal;
