import {
  Box,
  Button,
  Grid,
  MenuItem,
  Paper,
  Select,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import React, { useEffect, useRef, useState } from "react";
import CustomFormLabel from "./Common/CustomFormLabel";
import CustomTextField from "./Common/CustomTextField";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import "./Quill.css";
import axios from "axios";
import { API_PATHS, API_URL } from "../utils/apiUtils";
import DragDrop from "./Common/DragDrop";
import { FileUploader } from "react-drag-drop-files";
import { useParams } from "react-router-dom";

export const EditSubCategory = () => {
  const fileTypes = ["JPG", "PNG", "GIF"];

  const params = useParams();
  const id = params.id;

  const nameRef = useRef();
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [selectedSubProduct, setSelectedSubProduct] = useState(null);
  const colorRef = useRef();
  const [file, setFile] = useState(null);

  const [subCategories, setSubCategories] = useState([]);
  const [selectedSubCategory, setSelectedSubCategory] = useState(null);

  console.log("selectedSubProduct-->", selectedSubProduct);

  const getAllCategories = async () => {
    const response = await axios.get(API_PATHS.GET_ALL_CATEGORIES);
    const categoriesData = response.data.result.map((item) => ({
      id: item._id,
      name: item.name,
      master_folder_name: item.master_folder_name,
    }));

    setCategories(categoriesData);
  };

  const getSelectedSubCatData = async () => {
    const response = await axios.get(API_PATHS.GET_PRODUCTS_SUBCATEGORY);
    const selectedCat = response.data.result.find((item) => item._id == id);
    console.log("selectedCat-->", selectedCat);

    nameRef.current.value = selectedCat.category;
    colorRef.current.value = selectedCat.color;
    setSelectedSubProduct(selectedCat);
    setSelectedCategory(selectedCat.product_category_type);
  };

  console.log("selectedCategory-->", selectedCategory);

  useEffect(() => {
    getAllCategories();
    getSelectedSubCatData();
  }, []);

  const handleChange = async (file) => {
    setFile(file);
    let formData = new FormData();
    formData.append("file", file);
    formData.append("sub_cat_id", id);
    try {
      const res = await axios.post(
        API_PATHS.INSERT_SUB_CATEGORY_IMAGE,
        formData
      );
      alert("Image uploaded successfully!");
    } catch (err) {
      console.log(err.message);
    }
  };

  const handleChangeCategory = async (e) => {
    setSelectedCategory(e.target.value);
    const subCategoriesData = await axios.post(
      API_PATHS.GET_PEN_SUB_CATEGORIES,
      { category_id: e.target.value }
    );

    console.log("subCategory-->", subCategoriesData);

    const temp = subCategoriesData.data.result.map((item) => ({
      id: item._id,
      category: item.category,
    }));

    setSubCategories(temp);
  };

  const handleSubmit = async () => {
    const data = {
      category: nameRef.current.value,
      color: colorRef.current.value,
    };

    try {
      const res = await axios.patch(
        API_PATHS.UPDATE_SUB_CATEGORY + "/" + id,
        data
      );
      alert("Sub-category updated successfully!");
    } catch (err) {
      alert("Error, failed to update sub-category");
    }
  };

  return (
    <Box sx={{ width: "calc(100% - 230px)", m: 2 }}>
      {/* <Box
          sx={{
            flexDirection: "row-reverse",
            display: "flex",
          }}
        >
          <Button variant="contained">Add</Button>
        </Box> */}

      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Add Sub-category</Typography>
      </Box>
      <Paper
        elevation={3}
        sx={{
          paddingY: 2,
          paddingX: 4,
          boxShadow:
            "0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12)",
        }}
        variant="outlined"
      >
        {/* ------------------------------------------------------------------------------------------------ */}
        {/* Basic Layout */}
        {/* ------------------------------------------------------------------------------------------------ */}
        <Grid container>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-title" sx={{ mt: 0 }}>
              Sub category name
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-name"
              placeholder="John Deo"
              inputRef={nameRef}
              fullWidth
            />
          </Grid>

          <Grid item xs={12} alignItems="center">
            <CustomFormLabel htmlFor="demo-simple-select">
              Category type
            </CustomFormLabel>
            <Select
              labelId="demo-simple-select-label"
              disabled
              id="demo-simple-select"
              value={selectedCategory}
              onChange={(e) => handleChangeCategory(e)}
              color="error"
              fullWidth
              size="small"
            >
              {categories.map((item) => (
                <MenuItem key={item.id} value={item.id}>
                  {item.name}
                </MenuItem>
              ))}
            </Select>
          </Grid>

          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-color">Color</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-color"
              placeholder="color"
              fullWidth
              inputRef={colorRef}
            />
          </Grid>

          {/* 4 */}
          {/* <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-phone">Description</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              //   value={blogDate}
              type="date"
              id="fs-date"
              placeholder="John Deo"
              fullWidth
            />
          </Grid> */}

          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-root">
              Sub-category image
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            {selectedSubProduct && (
              <img
                src={`${API_URL}/${selectedSubProduct.master_folder_name}/${selectedSubProduct.file_name}`}
                style={{ objectFit: "contain" }}
                width={200}
                height={200}
              />
            )}
          </Grid>
          <Grid item xs={12}>
            <FileUploader
              handleChange={handleChange}
              name="file"
              types={fileTypes}
            />
          </Grid>

          {/* <Grid item xs={12}>
            <Autocomplete
              multiple
              id="blog-tags"
              options={[]}
              value={selectedTags}
              freeSolo
              onChange={(e, value) => setSelectedTags(value)}
              renderInput={(params) => (
                <CustomTextField {...params} placeholder="Add your tags..." />
              )}
            />
          </Grid> */}

          <Grid item xs={12} mt={3}>
            <Button
              variant="contained"
              color="primary"
              sx={{ fontWeight: "600", background: "#ed1b24" }}
              onClick={handleSubmit}
            >
              Update
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  );
};
