import React, { useState } from "react";
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Menu,
  MenuItem,
  Avatar,
  Button,
  Box,
  Tooltip,
} from "@mui/material";
import {
  Home as HomeIcon,
  Search as SearchIcon,
  Add as AddIcon,
  Person,
  Image,
  LogoutOutlined,
  ReceiptLongOutlined,
  FavoriteBorderOutlined,
  SettingsAccessibilityOutlined,
} from "@mui/icons-material";
import HouseOutlinedIcon from "@mui/icons-material/HouseOutlined";
import NotificationsNoneOutlinedIcon from "@mui/icons-material/NotificationsNoneOutlined";
import { Link, useNavigate } from "react-router-dom";
import language from "../../utils/language";
import SvgIcons from "./SvgIcons";

import ContactPageIcon from "@mui/icons-material/ContactPage";
import CategoryIcon from "@mui/icons-material/Category";
import InventoryIcon from "@mui/icons-material/Inventory";
import AppsIcon from "@mui/icons-material/Apps";
import WorkIcon from "@mui/icons-material/Work";

const NavigationBar = ({ title }) => {
  const navigateTo = useNavigate();

  const auth = JSON.parse(sessionStorage.getItem("user"));

  const isNursingHome = auth.role === "administrative" ? true : false;

  const [menu, setMenu] = React.useState(null);

  const userDetails = JSON.parse(sessionStorage.getItem("user"));

  const open = Boolean(menu);

  const handleClick = (event) => {
    setMenu(event.currentTarget);
  };

  const handleClose = () => {
    setMenu(null);
  };

  const logout = () => {
    sessionStorage.removeItem("user");
    navigateTo("/login");
  };

  const openTNC = () => {
    navigateTo("/tnc");
  };

  return (
    <AppBar position="static" style={{ background: "#ed1b24" }}>
      <Toolbar>
        {/* <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}> */}
        {/* <Link
            style={{
              color: "#fff",
              textDecoration: "none",
              display: "inline-block",
            }}
            to={"/"}
          >
            {title || "Senior Life"}
          </Link> */}
        {/* </Typography> */}
        <Box sx={{ flexGrow: 1 }}>
          {/* <SvgIcons icon="slIconWordmark" /> */}
          <img
            src="https://www.luxorpen.com/images/logo/logo.png"
            width={150}
          />
        </Box>
        <Tooltip title="Home">
          <IconButton
            color="inherit"
            LinkComponent={Link}
            to="/"
            // onClick={handleMenuOpen}
          >
            <HomeIcon />
          </IconButton>
        </Tooltip>

        <Tooltip title="Categories">
          <IconButton
            color="inherit"
            LinkComponent={Link}
            to="/categories"
            // onClick={handleMenuOpen}
          >
            <CategoryIcon />
          </IconButton>
        </Tooltip>

        <Tooltip title="Product sub categories">
          <IconButton
            color="inherit"
            LinkComponent={Link}
            to="/sub-category"
            // onClick={handleMenuOpen}
          >
            <AppsIcon />
          </IconButton>
        </Tooltip>

        <Tooltip title="Products">
          <IconButton
            color="inherit"
            LinkComponent={Link}
            to="/products"
            // onClick={handleMenuOpen}
          >
            <InventoryIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="Contacted List">
          <IconButton
            color="inherit"
            LinkComponent={Link}
            to="/contacted-list"
            // onClick={handleMenuOpen}
          >
            <ContactPageIcon />
          </IconButton>
        </Tooltip>
        <Tooltip title="Contacted List">
          <IconButton
            color="inherit"
            LinkComponent={Link}
            to="/career-list"
            // onClick={handleMenuOpen}
          >
            <WorkIcon />
          </IconButton>
        </Tooltip>
        {/* <Tooltip title="Notifications">
          <IconButton
            color="inherit"
            LinkComponent={Link}
            to="/notifications"
            // onClick={handleMenuOpen}
          >
            <NotificationsNoneOutlinedIcon />
          </IconButton>
        </Tooltip>

        <Tooltip title="All Users">
          <IconButton
            color="inherit"
            LinkComponent={Link}
            to="/users"
            // onClick={handleMenuOpen}
          >
            <Person />
          </IconButton>
        </Tooltip> */}
        {/* <IconButton
          color="inherit"
          LinkComponent={Link}
          to="/search-resident"
          // onClick={handleMenuOpen}
        >
          <SearchIcon />
        </IconButton> */}

        {/* <IconButton color="inherit" onClick={logout}>
          <LogoutOutlined />
        </IconButton> */}

        <IconButton
          id="basic-button"
          aria-controls={open ? "basic-menu" : undefined}
          aria-haspopup="true"
          aria-expanded={open ? "true" : undefined}
          onClick={handleClick}
        >
          <img
            src={
              userDetails.role === "nurse"
                ? "nurseProfile.png"
                : userDetails.role === "relative"
                ? "patient.jpg"
                : "profile.png"
            }
            alt="Button Image"
            width={35}
            height={35}
            style={{ borderRadius: "17px" }}
          />
        </IconButton>

        <Menu
          id="basic-menu"
          anchorEl={menu}
          open={open}
          onClose={handleClose}
          MenuListProps={{
            "aria-labelledby": "basic-button",
          }}
        >
          {console.log("UserDetails-->", isNursingHome)}
          <MenuItem>{`Hi, ${userDetails.userName}`}</MenuItem>
          {/* <MenuItem>{`Trabajo : ${language[userDetails.role]}`}</MenuItem> */}
          <MenuItem>{`Email : ${userDetails.email}`}</MenuItem>
          {/* <MenuItem onClick={openTNC}>Terms And Condition</MenuItem> */}
          <MenuItem onClick={logout}>Sign Out</MenuItem>
        </Menu>

        {/* <Button style={{ color: "white", fontWeight: "bold" }} onClick={logout}>
          Logout
        </Button> */}

        {/*    <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={handleMenuClose}
        >
          <MenuItem onClick={handleMenuClose}>Home</MenuItem>
          <MenuItem onClick={handleMenuClose}>Search</MenuItem>
          <MenuItem onClick={handleMenuClose}>Add</MenuItem>
        </Menu> */}
      </Toolbar>
    </AppBar>
  );
};

export default NavigationBar;
