import * as React from "react";
import Backdrop from "@mui/material/Backdrop";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Fade from "@mui/material/Fade";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import { API_BASE_URL, API_PATHS } from "../../utils/apiUtils";
import axios from "axios";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 900,
  height: 600,
  bgcolor: "background.paper",
  border: "2px solid #fff",
  borderRadius: 9,
  boxShadow: 24,
  p: 4,
};

export default function StampModal({
  open,
  setOpen,
  handleClose,
  handleOpen,
  stamps,
  productId,
}) {
  const [stampList, setStampList] = React.useState([
    {
      name: "acrylic-fiber-tip.png",
    },
    {
      name: "polyester-fibre-tip.png",
    },
  ]);

  React.useEffect(() => {
    setStampList((pre) => {
      return pre.map((t) => {
        if (stamps.includes(t.name)) {
          t.selected = true;
        }
        return t;
      });
    });
  }, [stamps]);

  const handleOnSelect = (item) => {
    setStampList([
      ...stampList.map((t) => {
        if (item.name == t.name) {
          t.selected = !t.selected;
        }
        return t;
      }),
    ]);
  };

  const handleSubmit = async () => {
    const filteredStamps = stampList
      .filter((item) => item.selected === true)
      .map((item) => item.name);

    try {
      const res = await axios.patch(`${API_PATHS.ADD_STAMPS}/${productId}`, {
        stampData: filteredStamps,
      });
      alert("Stamps added successfully!");
      handleClose();
    } catch (err) {
      alert("Error, cannot add stamps!");
    }
  };

  return (
    <div>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        open={open}
        onClose={handleClose}
        closeAfterTransition
        slots={{ backdrop: Backdrop }}
        slotProps={{
          backdrop: {
            timeout: 500,
          },
        }}
      >
        <Fade in={open}>
          <Box sx={style}>
            <Typography id="transition-modal-title" variant="h5" component="h5">
              Select Stamps
            </Typography>
            <div className="stamps-container">
              {stampList.map((item, i) => (
                <img
                  key={i + item.name}
                  className={
                    item.selected ? "selected-stamps stamps" : "stamps"
                  }
                  onClick={() => handleOnSelect(item)}
                  src={API_BASE_URL + "/stamps/" + item.name}
                  width={100}
                  height={100}
                ></img>
              ))}
            </div>
            <Button
              style={{
                background: "#ed1b24",
                color: "#fff",
                position: "absolute",
                bottom: 30,
                right: 30,
              }}
              onClick={handleSubmit}
            >
              Submit
            </Button>
          </Box>
        </Fade>
      </Modal>
    </div>
  );
}
