import {
  Box,
  Button,
  Grid,
  IconButton,
  MenuItem,
  Paper,
  Select,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import React, { useEffect, useRef, useState } from "react";

import CustomFormLabel from "./Common/CustomFormLabel";
import CustomTextField from "./Common/CustomTextField";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import "./Quill.css";
import axios from "axios";
import { API_PATHS, API_URL } from "../utils/apiUtils";
import DragDrop from "./Common/DragDrop";
import { useParams } from "react-router-dom";
import ColorPicker from "./Common/ColorPicker";
import AddIcon from "@mui/icons-material/Add";
import OtherImages from "./Common/OtherImages";
import StampModal from "./Common/StampModal";

export const EditProduct = () => {
  const router = useParams();

  const nameRef = useRef();
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const colorRef = useRef();
  const descriptionRef = useRef();
  const iconRef = useRef();
  const didYouKnowRef = useRef();

  const UpdateProductId = router.id;
  const [product, setProduct] = useState([]);
  const [mainCategoryId, setMainCategoryId] = useState("");
  const [categoryId, setCategoryId] = useState("");
  const [fileName, setFileName] = useState("");
  const [rootFolderName, setRootFolderName] = useState("");
  const [rows, setRows] = useState([]);
  const [otherImagesRows, setOtherImagesRows] = useState([]);
  const [count, setCount] = useState(0);
  const [stamps, setStamps] = useState([]);

  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  console.log("UpdateProductId-->", UpdateProductId);
  console.log("selectedCategory-->", selectedCategory);
  // console.log("product-->", product);

  const [subCategories, setSubCategories] = useState([]);
  const [selectedSubCategory, setSelectedSubCategory] = useState(null);
  // console.log("selectedSubCategories-->", selectedSubCategory);
  console.log("image-->", `${API_URL}/${rootFolderName}/${fileName}`);

  const columns = [
    { field: "id", headerName: "ID", width: 100 },
    { field: "label", headerName: "Label", width: 70 },
    {
      field: "hexcode",
      headerName: "Hexcode",
      width: 130,
      renderCell: (params) => {
        console.log(params);
        return (
          <>
            {params.value}
            <span
              style={{
                backgroundColor: params.value,
                marginLeft: 4,
                width: 20,
                height: 20,
                borderRadius: "50%",
              }}
            />
          </>
        );
      },
    },
    { field: "root_folder_name", headerName: "Root folder name", width: 420 },
    { field: "file_name", headerName: "File name", width: 290 },
  ];

  const otherImagesColumns = [
    { field: "id", headerName: "ID", width: 100 },
    {
      field: "image",
      headerName: "Image",
      width: 100,
      renderCell: ({ row }) => (
        <div>
          <img
            src={`${API_URL}/${row.root_folder_name}${row.file_name}`}
            style={{ borderRadius: "50%" }}
            width={40}
            height={40}
          />
        </div>
      ),
    },
    { field: "root_folder_name", headerName: "Root folder name", width: 420 },
    { field: "file_name", headerName: "File name", width: 290 },
  ];

  const updateHandler = async () => {
    const data = {
      name: nameRef.current.value,
      category_type: selectedSubCategory,
      color: colorRef.current.value,
      description: descriptionRef.current.value,
      icon: iconRef.current.value,
      did_you_know: didYouKnowRef.current.value,
      main_category_type: selectedCategory,
    };

    console.log("edit submit data-->", data);

    try {
      const updatedData = await axios.post(
        `${API_URL}/update-product/${UpdateProductId}`,
        data
      );
      alert("successfully updated!");
    } catch (err) {
      console.log("error");
    }
  };

  const getAllCategories = async () => {
    const response = await axios.get(API_PATHS.GET_ALL_CATEGORIES);
    const categoriesData = response.data.result.map((item) => ({
      id: item._id,
      name: item.name,
    }));

    setCategories(categoriesData);
  };
  const getProductById = async (id, subCategory) => {
    const response = await axios.post(API_PATHS.GET_PRODUCT_BY_ID, { id });
    const categoryType = await axios.get(API_PATHS.GET_ALL_CATEGORIES);
    console.log("edit product--->", response.data.result);
    setMainCategoryId(response.data.result.main_category_type);
    setCategoryId(response.data.result.category_type);
    setStamps(response.data.result.stamps);
    console.log("color-variants==>", response.data.result.color_variant);
    const CatType = categoryType.data.result.filter(
      (item) => response.data.result.main_category_type == item._id
    );

    console.log("Categories-->", categoryType);
    console.log("CatType-->", CatType);
    nameRef.current.value = response.data.result.name;
    // setSelectedCategory(CatType[0].name);
    if (CatType.length > 0) {
      setSelectedCategory(CatType[0]._id);
    }
    colorRef.current.value = response.data.result.color;
    descriptionRef.current.value = response.data.result.description;
    iconRef.current.value = response.data.result.icon;
    didYouKnowRef.current.value = response.data.result.did_you_know;

    setProduct(response.data.result);
    setFileName(response.data.result.file_name);
    setRootFolderName(response.data.result.root_folder_name);
    console.log("color-variants-->", response.data.result.color_variant);

    //setting sub category when pre fetching
    const subCategoriesData = await axios.post(
      API_PATHS.GET_PEN_SUB_CATEGORIES,
      { category_id: response.data.result.main_category_type }
    );

    console.log("subCategory-->", subCategoriesData);

    const temp = subCategoriesData.data.result.map((item) => ({
      id: item._id,
      category: item.category,
    }));

    const productSubCategory = temp.filter(
      (item) => item.id == response.data.result.category_type
    );
    setSubCategories(temp);
    if (productSubCategory.length > 0) {
      setSelectedSubCategory(productSubCategory[0].id);
    }

    // const color_variants = response.data.result.color_variant.map((item) => {
    //   if (!item.hasOwnProperty("isOtherImage")) {
    //     return {
    //       ...item,
    //       id: `CV${item.id.slice(0, 5)}`,
    //     };
    //   } else {
    //     return {};
    //   }
    // });
    let color_variants = response.data.result.color_variant.filter(
      (item) => !item.hasOwnProperty("isOtherImage")
    );

    color_variants = color_variants.map((item) => ({
      ...item,
      id: `CV${item.id.slice(0, 5)}`,
    }));
    // console.log("color_variants-->", color_variants);
    setRows(color_variants);

    let other_images = response.data.result.color_variant.filter((item) =>
      item.hasOwnProperty("isOtherImage")
    );

    other_images = other_images.map((item) => ({
      ...item,
      id: `CV${item.id.slice(0, 5)}`,
      image: `${API_URL}/${item.root_folder_name} ${item.file_name}`,
    }));
    console.log("other_images-->", other_images);

    setOtherImagesRows(other_images);
  };

  const getSelectedSubCategory = async () => {
    if (mainCategoryId !== categoryId) {
      const subCategoriesData = await axios.post(
        API_PATHS.GET_PEN_SUB_CATEGORIES,
        { category_id: mainCategoryId }
      );
      let temp = subCategoriesData.data.result.map((item) => ({
        id: item._id,
        category: item.category,
      }));

      const tempSubCatDataByProduct = temp.filter(
        (item) => item.id == categoryId
      );

      console.log("subSelected-->", tempSubCatDataByProduct);

      setSelectedSubCategory(tempSubCatDataByProduct[0].id);
    }
  };

  const getSubCategoryData = async (data) => {
    const subCategoriesData = await axios.post(
      API_PATHS.GET_PEN_SUB_CATEGORIES,
      { category_id: data }
    );

    console.log("subCategory-->", subCategoriesData);

    const temp = subCategoriesData.data.result.map((item) => ({
      id: item._id,
      category: item.category,
    }));

    setSubCategories(temp);
  };

  useEffect(() => {
    getAllCategories();
    getProductById(UpdateProductId);
  }, [count]);

  const handleChangeCategory = async (e) => {
    setSelectedCategory(e.target.value);
    const subCategoriesData = await axios.post(
      API_PATHS.GET_PEN_SUB_CATEGORIES,
      { category_id: e.target.value }
    );

    console.log("subCategory-->", subCategoriesData);

    const temp = subCategoriesData.data.result.map((item) => ({
      id: item._id,
      category: item.category,
    }));

    setSubCategories(temp);
  };

  // const handleSubmit = async () => {
  //   const data = {
  //     name: nameRef.current.value,
  //     category_type: selectedCategory,
  //     color: colorRef.current.value,
  //     description: descriptionRef.current.value,
  //     icon: iconRef.current.value,
  //     did_you_know: didYouKnowRef.current.value,
  //     file_name: fileNameRef.current.value,
  //     root_folder_name: rootFolderNameRef.current.value,
  //   };

  //   const res = await axios.post(API_PATHS.ADD_PRODUCT, data);
  // };

  return (
    <Box sx={{ width: "calc(100% - 230px)", m: 2 }}>
      {/* <Box
          sx={{
            flexDirection: "row-reverse",
            display: "flex",
          }}
        >
          <Button variant="contained">Add</Button>
        </Box> */}

      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Edit Product</Typography>
      </Box>
      <Paper
        elevation={3}
        sx={{
          paddingY: 2,
          paddingX: 4,
          boxShadow:
            "0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12)",
        }}
        variant="outlined"
      >
        {/* ------------------------------------------------------------------------------------------------ */}
        {/* Basic Layout */}
        {/* ------------------------------------------------------------------------------------------------ */}
        <Grid container>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-title" sx={{ mt: 0 }}>
              Name
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-name"
              placeholder="John Deo"
              inputRef={nameRef}
              fullWidth
            />
          </Grid>
          <Grid item xs={12} alignItems="center">
            <CustomFormLabel htmlFor="demo-simple-select">
              Category type
            </CustomFormLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={selectedCategory}
              onChange={(e) => handleChangeCategory(e)}
              color="error"
              fullWidth
              size="small"
            >
              {categories.map((item) => (
                <MenuItem key={item.id} value={item.id}>
                  {item.name}
                </MenuItem>
              ))}
            </Select>
          </Grid>
          <Grid item xs={12} alignItems="center">
            <CustomFormLabel htmlFor="demo-simple-select">
              Sub-category type
            </CustomFormLabel>

            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={selectedSubCategory}
              onChange={(e) => setSelectedSubCategory(e.target.value)}
              color="error"
              fullWidth
              size="small"
            >
              {subCategories.map((item) => (
                <MenuItem key={item.id} value={item.id}>
                  {item.category}
                </MenuItem>
              ))}
            </Select>
          </Grid>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-color">Color</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-color"
              placeholder="color"
              fullWidth
              inputRef={colorRef}
            />
          </Grid>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-description">
              Description
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <Paper variant="outlined">
              <CustomTextField
                multiline
                rows={4}
                fullWidth
                // onChange={(value) => setQuillText(value)}
                inputRef={descriptionRef}
                placeholder="Description"
              />
            </Paper>
          </Grid>
          {/* 4 */}
          {/* <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-phone">Description</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              //   value={blogDate}
              type="date"
              id="fs-date"
              placeholder="John Deo"
              fullWidth
            />
          </Grid> */}
          {/* 5 */}
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-message">Icon</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-icon"
              fullWidth
              inputRef={iconRef}
              //   onChange={handleChooseImage}
            />
            {/* <Button onClick={async () => console.log(await uploadImageToFirebase(blogImage))}>
            Upload
          </Button> */}
          </Grid>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-did">Did you know</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField id="bl-did" fullWidth inputRef={didYouKnowRef} />
          </Grid>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-root">Product image</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <img
              src={`${API_URL}/${rootFolderName}/${fileName}`}
              style={{ objectFit: "contain" }}
              width={200}
              height={200}
            />
            <DragDrop
              product_id={UpdateProductId}
              _cat_id={
                selectedSubCategory ? selectedSubCategory : selectedCategory
              }
            />
          </Grid>
          <OtherImages
            product_id={UpdateProductId}
            _cat_id={
              selectedSubCategory ? selectedSubCategory : selectedCategory
            }
            setCount={setCount}
          />
          <Grid item xs={12}>
            <CustomFormLabel htmlFor="bl-table">
              Other images table
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <DataGrid
              rows={otherImagesRows}
              columns={otherImagesColumns}
              sx={{
                boxShadow: 2,
                "& .MuiDataGrid-cell:hover": {
                  color: "primary.main",
                },
                "& .MuiDataGrid-columnHeaders": {
                  color: "white",
                  backgroundColor: "#ed1b24",
                },
              }}
              initialState={{
                pagination: {
                  paginationModel: { page: 0, pageSize: 2 },
                },
              }}
              // checkboxSelection
            />
          </Grid>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-root">
              Add Color Variants
            </CustomFormLabel>
          </Grid>

          {/* //-----------Color-variant----------// */}
          <Grid item xs={12}>
            <ColorPicker
              product_id={UpdateProductId}
              _cat_id={
                selectedSubCategory ? selectedSubCategory : selectedCategory
              }
              setRows={setRows}
              rows={rows}
              setCount={setCount}
            />
          </Grid>

          {/* ====Color-variant-data-table==== */}

          <Grid item xs={12}>
            <CustomFormLabel htmlFor="bl-table">
              Color variants table
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <DataGrid
              rows={rows}
              columns={columns}
              sx={{
                boxShadow: 2,
                "& .MuiDataGrid-cell:hover": {
                  color: "primary.main",
                },
                "& .MuiDataGrid-columnHeaders": {
                  color: "white",
                  backgroundColor: "#ed1b24",
                },
              }}
              initialState={{
                pagination: {
                  paginationModel: { page: 0, pageSize: 2 },
                },
              }}
              // checkboxSelection
            />
          </Grid>

          {/* stamps */}
          <Grid item xs={12}>
            <CustomFormLabel htmlFor="bl-table">Add Stamps</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <Button
              variant="contained"
              onClick={handleOpen}
              style={{ background: "#ed1b24" }}
            >
              Select Stamps
            </Button>
          </Grid>
          <StampModal
            open={open}
            setOpen={setOpen}
            handleClose={handleClose}
            handleOpen={handleOpen}
            stamps={stamps}
            productId={UpdateProductId}
          />

          {/* <Grid item xs={12}>
            <Autocomplete
              multiple
              id="blog-tags"
              options={[]}
              value={selectedTags}
              freeSolo
              onChange={(e, value) => setSelectedTags(value)}
              renderInput={(params) => (
                <CustomTextField {...params} placeholder="Add your tags..." />
              )}
            />
          </Grid> */}
          <Grid item xs={12} mt={3}>
            <Button
              variant="contained"
              color="primary"
              sx={{ fontWeight: "600", background: "#ed1b24" }}
              onClick={updateHandler}
            >
              Update
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  );
};
