import {
  Box,
  Button,
  Grid,
  MenuItem,
  Paper,
  Select,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { DataGrid } from "@mui/x-data-grid";
import React, { useEffect, useRef, useState } from "react";
import CustomFormLabel from "./Common/CustomFormLabel";
import CustomTextField from "./Common/CustomTextField";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import "./Quill.css";
import axios from "axios";
import { API_PATHS } from "../utils/apiUtils";
import DragDrop from "./Common/DragDrop";
import { FileUploader } from "react-drag-drop-files";

export const AddCategory = () => {
  const fileTypes = ["JPG", "PNG", "GIF"];
  const [name, setName] = useState("");
  const [file, setFile] = useState(null);

  const handleSubmit = async () => {
    try {
      const res = await axios.post(API_PATHS.INSERT_CATEGORY, { name });
      alert("Category added successfully!");
    } catch (err) {
      alert("Error, while adding category");
    }
  };

  return (
    <Box sx={{ width: "calc(100% - 230px)", m: 2 }}>
      {/* <Box
          sx={{
            flexDirection: "row-reverse",
            display: "flex",
          }}
        >
          <Button variant="contained">Add</Button>
        </Box> */}

      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Add Category</Typography>
      </Box>
      <Paper
        elevation={3}
        sx={{
          paddingY: 2,
          paddingX: 4,
          boxShadow:
            "0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12)",
        }}
        variant="outlined"
      >
        {/* ------------------------------------------------------------------------------------------------ */}
        {/* Basic Layout */}
        {/* ------------------------------------------------------------------------------------------------ */}
        <Grid container>
          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-title" sx={{ mt: 0 }}>
              Name
            </CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <CustomTextField
              id="bl-name"
              placeholder="John Deo"
              value={name}
              onChange={(e) => setName(e.target.value)}
              fullWidth
            />
          </Grid>

          <Grid item xs={12} display="flex" alignItems="center">
            <CustomFormLabel htmlFor="bl-root">Product image</CustomFormLabel>
          </Grid>
          <Grid item xs={12}>
            <FileUploader
              //   handleChange={handleChange}
              name="file"
              types={fileTypes}
            />
          </Grid>

          {/* <Grid item xs={12}>
            <Autocomplete
              multiple
              id="blog-tags"
              options={[]}
              value={selectedTags}
              freeSolo
              onChange={(e, value) => setSelectedTags(value)}
              renderInput={(params) => (
                <CustomTextField {...params} placeholder="Add your tags..." />
              )}
            />
          </Grid> */}

          <Grid item xs={12} mt={3}>
            <Button
              onClick={handleSubmit}
              variant="contained"
              color="primary"
              sx={{ fontWeight: "600", background: "#ed1b24" }}
              //   onClick={handleSubmit}
            >
              Submit
            </Button>
          </Grid>
        </Grid>
      </Paper>
    </Box>
  );
};
