import { Button, Grid, IconButton, Paper } from "@mui/material";
import React, { useState } from "react";
import CustomFormLabel from "./CustomFormLabel";
import CustomTextField from "./CustomTextField";
import AddIcon from "@mui/icons-material/Add";
import ColorVariantImagePicker from "./ColorVariantImagePicker";
import { FileUploader } from "react-drag-drop-files";
import axios from "axios";
import { API_PATHS } from "../../utils/apiUtils";
const ColorPicker = ({ _cat_id, product_id, setCount }) => {
  const fileTypes = ["JPG", "PNG", "GIF"];

  const [selectedColor, setSelectedColor] = useState("");
  const [label, setLabel] = useState("");
  const [file, setFile] = useState(null);

  const handleChangeColor = async (e) => {
    const timer = setTimeout(() => {
      setSelectedColor(e.target.value);
    }, 1000);
  };

  const handleChange = async (file) => {
    setFile(file);
  };

  const handleSubmit = async () => {
    let formData = new FormData();
    formData.append("file", file);
    formData.append("product_id", product_id);
    formData.append("_cat_id", _cat_id);
    formData.append("label", label);
    formData.append("hexcode", selectedColor);

    try {
      const response = await axios.post(
        API_PATHS.INSERT_CAT_PRODUCTS_COLOR_VARIANT_FOR_PEN,
        formData
      );
      setCount((item) => item + 1);
      alert("Color variant added!");
    } catch (err) {
      console.log("error->", err.message);
    }
  };

  const addColorHandler = () => {};
  return (
    <div>
      <Paper
        elevation={3}
        sx={{
          paddingY: 2,
          paddingTop: 0,
          paddingX: 4,
          width: "40%",
          boxShadow:
            "0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12)",
        }}
        variant="outlined"
      >
        <Grid item xs={12} display="flex" alignItems="center">
          <CustomFormLabel htmlFor="bl-color-label">Label</CustomFormLabel>
        </Grid>
        <Grid item xs={12}>
          <CustomTextField
            htmlFor="bl-color-label"
            onChange={(e) => setLabel(e.target.value)}
          ></CustomTextField>
        </Grid>
        <Grid item xs={12} display="flex" alignItems="center">
          <CustomFormLabel htmlFor="bl-color-name">
            Color in Hexcode
          </CustomFormLabel>
        </Grid>
        <Grid item xs={12}>
          <CustomTextField
            disabled
            htmlFor="bl-color-name"
            value={selectedColor}
          ></CustomTextField>
        </Grid>
        <Grid item xs={12} display="flex" alignItems="center">
          <CustomFormLabel htmlFor="bl-color-name">
            Color picker
          </CustomFormLabel>
        </Grid>
        <Grid
          item
          xs={12}
          sx={{ display: "flex", justifyContent: "space-between" }}
        >
          <input
            type="color"
            onChange={handleChangeColor}
            defaultValue={"#ffffff"}
          />
        </Grid>
        <Grid item xs={12} marginTop={4}>
          <CustomFormLabel htmlFor="bl-color-name">
            Upload different color images
          </CustomFormLabel>
          <FileUploader
            handleChange={handleChange}
            name="file"
            types={fileTypes}
          />
          {/* <ColorVariantImagePicker
            _cat_id={_cat_id}
            product_id={product_id}
            hexcode={selectedColor}
            label={label}
          /> */}
          <div style={{ textAlign: "right" }}>
            <Button
              variant="contained"
              size="small"
              sx={{ marginTop: 2, background: "#ed1b24" }}
              onClick={handleSubmit}
            >
              Add
            </Button>
          </div>
        </Grid>
      </Paper>
    </div>
  );
};

export default ColorPicker;
