import {
  Button,
  Container,
  Grid,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { API_PATHS } from "../utils/apiUtils";

const TermsAndConditions = () => {
  const [formData, setFormData] = useState({
    title: "",
    description: "",
  });
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  useEffect(() => {
    getTermsAndConditions();
  }, []);

  const getTermsAndConditions = async () => {
    try {
      const response = await axios.post(API_PATHS.GET_TERMS_AND_CONDITION);
      console.log("TNC-->", response.data);
      setFormData({
        title: response.data.title,
        description: response.data.description,
      });
    } catch (error) {
      console.log("Error In GET TNC->-->", error);
    }
  };
  const editTermsAndConditions = async (e) => {
    e.preventDefault();
    try {
      const updatedTermsAndCondition = {
        title: formData.title,
        description: formData.description,
      };
      const response = await axios.post(
        API_PATHS.EDIT_TERMS_AND_CONDITION,
        updatedTermsAndCondition
      );
      if (response.status === 200) {
        setSnackbarMessage("Updated Successfully");
        setSnackbarOpen(true);
      }
    } catch (error) {
      console.log("Error In Edit TNC->", error);
    }
  };

  //   const handleSubmit = (e) => {
  //     e.preventDefault();
  //     // Handle form submission here, e.g., send data to the server
  //     console.log(formData);
  //     // Reset the form
  //     setFormData({
  //       title: "",
  //       description: "",
  //     });
  //   };

  const handleSnackbarClose = () => {
    setSnackbarOpen(false); // Close the Snackbar
  };

  return (
    <Container maxWidth="sm" sx={{ mt: 5 }}>
      <Snackbar
        open={snackbarOpen}
        autoHideDuration={500} // Adjust the duration as needed
        onClose={handleSnackbarClose}
        message={snackbarMessage}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
        style={{ backgroundColor: "green" }}
        color="green"
      />
      <Typography variant="h3" textAlign="center">
        Terms And Conditions
      </Typography>
      <form onSubmit={editTermsAndConditions}>
        <Grid container spacing={2} sx={{ mt: 2 }}>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Title"
              name="title"
              value={formData.title}
              onChange={handleChange}
              variant="outlined"
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              fullWidth
              label="Description"
              name="description"
              value={formData.description}
              onChange={handleChange}
              variant="outlined"
              multiline
              rows={4}
            />
          </Grid>
          <Grid item xs={12}>
            <Button type="submit" variant="contained" color="primary" fullWidth>
              Submit
            </Button>
          </Grid>
        </Grid>
      </form>
    </Container>
  );
};

export default TermsAndConditions;
