import {
  Box,
  Button,
  Card,
  CardContent,
  Checkbox,
  Container,
  FormControlLabel,
  Grid,
  InputLabel,
  Link,
  MenuItem,
  Select,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";

import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import LoadingModal from "../Common/LoadingModal";
import axios from "axios";
import { api_base_url } from "../../utils/apiUtils";
import language from "../../utils/language";
import Header from "../Common/Header";

const Register = () => {
  const location = useLocation();
  console.log("Loc--->", location);
  const auth = JSON.parse(sessionStorage.getItem("user"));
  const isSuperAdmin = auth.role === "superadmin" ? true : false;

  const { id, isEditing, formData } = location.state;
  const [firstName, setFirstName] = useState(
    formData && formData.first_name ? formData.first_name : ""
  );
  const [lastName, setLastName] = useState(
    formData && formData.last_name ? formData.last_name : ""
  );
  const [phoneNumber, setPhoneNumber] = useState(
    formData && formData.phone_number ? formData.phone_number : ""
  );
  const [address, setAddress] = useState(
    formData && formData.address ? formData.address : ""
  );
  const [email, setEmail] = useState(
    formData && formData.email ? formData.email : ""
  );
  const [password, setPassword] = useState("");
  const [role, setRole] = useState(
    formData && formData.role
      ? formData.role
      : isSuperAdmin
      ? "superadmin"
      : "none"
  );
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");

  const navigateTo = useNavigate();

  const handleJobArea = (event) => {
    setRole(event.target.value);
  };

  const handleSnackbarClose = () => {
    setSnackbarOpen(false); // Close the Snackbar
  };

  const handleEmailChange = (event) => {
    setEmail(event.target.value);
  };

  const handlePhoneNumberChange = (event) => {
    setPhoneNumber(event.target.value);
  };

  const handleAddressChange = (event) => {
    setAddress(event.target.value);
  };

  const handleFirstName = (event) => {
    setFirstName(event.target.value);
  };

  const handleLastName = (event) => {
    setLastName(event.target.value);
  };

  const handleUpdateUser = async (event) => {
    event.preventDefault();

    try {
      let response = await axios.put(
        `${api_base_url}/updateUser/${id}`,
        { email, password, phoneNumber, firstName, lastName, role, address },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      if (response.data.statusCode === 200) {
        const user = { ...response.data.data, token: response.data.token };
        // sessionStorage.setItem("user", JSON.stringify(user));
        setSnackbarMessage(response.data.message);
        setSnackbarOpen(true);

        setTimeout(() => {
          navigateTo("/users");
        }, 2000);
      } else if (response.data.statusCode === 400 || 500) {
        setSnackbarMessage(response.data.error);
        setSnackbarOpen(true);
        console.log("Error In Register-->", response.data.error);
      }
    } catch (err) {
      console.log("Error In Register Last Catch-->", err);
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();

    const nursing_home_id = auth.nursingHomeId;

    try {
      let response = await axios.post(
        `${api_base_url}/register`,
        {
          email,
          password,
          phoneNumber,
          firstName,
          lastName,
          role,
          address,
          nursing_home_id,
        },
        {
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      if (response.data.statusCode === 200) {
        const user = { ...response.data.data, token: response.data.token };
        // sessionStorage.setItem("user", JSON.stringify(user));
        setSnackbarMessage(response.data.message);
        setSnackbarOpen(true);

        setTimeout(() => {
          navigateTo("/users");
        }, 2000);
      } else if (response.data.statusCode === 400 || 500) {
        setSnackbarMessage(response.data.error);
        setSnackbarOpen(true);
        console.log("Error In Register-->", response.data.error);
      }
    } catch (err) {
      console.log("Error In Register Last Catch-->", err);
    }
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };
  return (
    <div style={{ background: "#FAFAFA" }}>
      {/* <Header /> */}
      <Container maxWidth="sm">
        <Snackbar
          open={snackbarOpen}
          autoHideDuration={2000} // Adjust the duration as needed
          onClose={handleSnackbarClose}
          message={snackbarMessage}
          anchorOrigin={{ vertical: "top", horizontal: "right" }}
          style={{ backgroundColor: "green" }}
          color="green"
        />
        <Box
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Card sx={{ marginTop: 4 }}>
            <CardContent>
              {/* <Box
                sx={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Typography component="h1" variant="h5">
                  Add User
                </Typography>
              </Box> */}

              <Box
                component="form"
                onSubmit={isEditing ? handleUpdateUser : handleSubmit}
              >
                <Grid container spacing={2} marginTop={1}>
                  <Grid item xs={6} sm={6}>
                    <TextField
                      value={firstName}
                      label={language.firstName}
                      fullWidth
                      required
                      onChange={handleFirstName}
                    />
                  </Grid>
                  <Grid item xs={6} sm={6}>
                    <TextField
                      value={lastName}
                      label={language.lastName}
                      fullWidth
                      required
                      onChange={handleLastName}
                    />
                  </Grid>
                </Grid>
                <TextField
                  value={phoneNumber}
                  label={language.phoneNumber}
                  fullWidth
                  margin="normal"
                  required
                  onChange={handlePhoneNumberChange}
                />
                <TextField
                  value={address}
                  label={language.address}
                  fullWidth
                  margin="normal"
                  required
                  onChange={handleAddressChange}
                />
                {!isSuperAdmin && (
                  <Select
                    labelId="dropdown-label"
                    id="dropdown"
                    value={role}
                    label="Select an option"
                    onChange={handleJobArea}
                    variant="outlined"
                    fullWidth
                    sx={{ mt: 1 }}
                  >
                    <MenuItem value="none" disabled>
                      {language.selectJobArea}
                    </MenuItem>
                    <MenuItem value="nurse">{language.nurse}</MenuItem>
                    <MenuItem value="supervision">
                      {language.supervision}
                    </MenuItem>
                    <MenuItem value="administrative">
                      {language.administrative}
                    </MenuItem>
                    <MenuItem value="relative">{language.relative}</MenuItem>
                  </Select>
                )}

                <TextField
                  value={email}
                  label={language.email}
                  type="email"
                  fullWidth
                  margin="normal"
                  required
                  onChange={handleEmailChange}
                />
                {!isEditing && (
                  <TextField
                    value={password}
                    label={language.password}
                    required
                    fullWidth
                    type="password"
                    margin="normal"
                    onChange={handlePasswordChange}
                  />
                )}

                {/* <FormControlLabel
                    control={
                      <Checkbox
                        checked={rememberMe}
                        onChange={handleRememberMeChange}
                        color="primary"
                      />
                    }
                    label="Remember Me"
                  /> */}

                <Grid container spacing={2} marginTop={1}>
                  <Grid item xs={6} sm={6}>
                    <Button
                      onClick={() => navigateTo("/users")}
                      variant="contained"
                      fullWidth
                      sx={{ height: "100%" }}
                    >
                      {language.back}
                    </Button>
                  </Grid>
                  <Grid item xs={6} sm={6}>
                    <Button type="submit" variant="contained" fullWidth>
                      {isEditing ? language.updateUser : language.addUser}
                    </Button>
                  </Grid>
                </Grid>

                {/* <Grid container>
                  <Grid item xs>
                      <Link href="#" variant="body2">
                        Forgot Password?
                      </Link>
                    </Grid>
                  <Grid item>
                    <Link href="/login" variant="body2">
                      {language.haveAccountLogin}
                    </Link>
                  </Grid>
                </Grid> */}
              </Box>
            </CardContent>
          </Card>
        </Box>
      </Container>
    </div>
  );
};

export default Register;
