import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
  IconButton,
  Slide,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { DataGrid } from "@mui/x-data-grid";
import DoneIcon from "@mui/icons-material/Done";
import { Clear } from "@mui/icons-material";
import RewardModal from "./Common/RewardModal";
import RejectReferralModal from "./Common/RejectReferralModal";
import { API_PATHS, API_URL } from "../utils/apiUtils";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import axios from "axios";
import date from "date-and-time";
import { useNavigate } from "react-router-dom";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import CustomTextField from "./Common/CustomTextField";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const Users = () => {
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  const [allTransactions, setAllTransactions] = useState("");
  const [allData, setAllData] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [open, setOpen] = React.useState(false);
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  const handleClickOpen = (id, name) => {
    setOpen({ name, id });
  };

  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    getAllTransactions();
  }, []);

  useEffect(() => {
    const filteredData = allData.filter((item) =>
      item.name.toLowerCase().includes(searchText.toLowerCase())
    );

    setAllTransactions(filteredData);
  }, [searchText]);

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  const handleDelete = async (id) => {
    console.log("sub-cat Deleted id -->", id);
    try {
      const res = await axios.delete(`${API_PATHS.DELETE_SUB_CATEGORY}/${id}`);
      handleClose();
      setTimeout(() => {
        alert("Successfully deleted.");
        getAllTransactions();
      }, 500);
    } catch (err) {
      handleClose();
      setTimeout(() => {
        alert("error, while deleting the sub-category!");
      }, 500);
    }
  };

  const getAllTransactions = async () => {
    try {
      console.log("Url Path-->", API_PATHS.GET_PRODUCTS_SUBCATEGORY);
      setLoading(true);
      const response = await axios.get(API_PATHS.GET_PRODUCTS_SUBCATEGORY);
      if (response.status === 200) {
        const transaction = response.data.result.map((item, index) => ({
          sno: index + 1,
          databaseId: item._id,
          id: `SUB${item._id.slice(-5)}`,
          _id: item._id,
          name: item.category,
          color: item.color,
          file_name: item.file_name ? item.file_name : "NA",
          master_folder_name: item.master_folder_name
            ? item.master_folder_name
            : "NA",
          product_folder_name: item.product_folder_name
            ? item.master_folder_name
            : "NA",
          product_category_type: item.product_category_type,
          created_on: date.format(
            new Date(item.created_on),
            "ddd, MMM DD YYYY hh:mm A"
          ),
        }));
        setAllTransactions(transaction);
        setLoading(false);
        setAllData(transaction);
      }
    } catch (error) {
      setLoading(false);
      console.log("Error In get all referrals-->", error);
    }
  };

  const columns = [
    {
      field: "action",
      headerName: "Action",
      sortable: false,
      renderCell: ({ row }) => (
        <>
          <IconButton onClick={() => navigate(`/edit-sub-category/${row._id}`)}>
            <EditIcon color="red" />
          </IconButton>
          <IconButton onClick={() => handleClickOpen(row._id, row.name)}>
            <DeleteIcon color="red" />
          </IconButton>
        </>
      ),
    },
    { field: "sno", headerName: "S.No", width: 70 },
    {
      field: "productImg",
      headerName: "Product image",
      width: 120,
      sortable: false,
      renderCell: ({ row }) => (
        <div>
          <img
            src={`${API_URL}/${row.master_folder_name}/${row.file_name}`}
            style={{ borderRadius: "50%" }}
            width={40}
            height={40}
          />
        </div>
      ),
    },
    { field: "id", headerName: "ID", width: 100 },
    { field: "name", headerName: "Name", width: 190 },
    { field: "color", headerName: "Color", width: 120 },
    { field: "file_name", headerName: "File name", width: 200 },
    {
      field: "master_folder_name",
      headerName: "Master folder name",
      width: 200,
    },
    {
      field: "product_folder_name",
      headerName: "Product folder name",
      width: 200,
    },
    { field: "created_on", headerName: "Created on", width: 230 },
  ];

  const exportCSV = (rows, columns) => {
    const csvRows = [];

    // Create the header row
    const exportedColumns = columns.filter((item) => !item.hideInExport);
    const header = exportedColumns.map((column) => column.headerName);
    csvRows.push(header.join(","));

    // Create the data rows
    rows.forEach((row) => {
      const rowData = exportedColumns.map((column) => {
        return row[column.field];
      });
      csvRows.push(rowData.join(","));
    });

    // Combine rows into a single CSV string
    const csvString = csvRows.join("\n");

    // Create a blob containing the CSV data
    const blob = new Blob([csvString], { type: "text/csv" });

    // Create a temporary URL to the blob
    const url = window.URL.createObjectURL(blob);

    // Create a link element to trigger the download
    const a = document.createElement("a");
    a.href = url;
    a.download = "Sub-category data.csv";

    // Trigger the click event on the link to initiate the download
    a.click();

    // Clean up the temporary URL
    window.URL.revokeObjectURL(url);
  };

  return (
    <Box sx={{ width: "calc(100% - 230px)", m: 2 }}>
      {/* <Box
          sx={{
            flexDirection: "row-reverse",
            display: "flex",
          }}
        >
          <Button variant="contained">Add</Button>
        </Box> */}

      <Snackbar
        open={snackbarOpen}
        autoHideDuration={1000} // Adjust the duration as needed
        onClose={handleSnackbarClose}
        message={snackbarMessage}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      />
      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Sub-categories</Typography>
        <div style={{ display: "flex" }}>
          <CustomTextField
            placeholder="Search..."
            onChange={(e) => setSearchText(e.target.value)}
          />
          <Button
            variant="contained"
            onClick={() => navigate("/add-sub-category")}
            style={{
              height: "80%",
              backgroundColor: "#ed1b24",
              marginRight: 10,
              marginLeft: 10,
            }}
          >
            Add Sub-category
          </Button>
          <Button
            variant="contained"
            onClick={() => exportCSV(allTransactions, columns)}
            style={{ height: "80%", backgroundColor: "#ed1b24" }}
          >
            Export To CSV
          </Button>
        </div>
      </Box>
      <Box sx={{ width: "100%" }}>
        <DataGrid
          autoHeight
          rows={allTransactions}
          columns={columns}
          loading={loading}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 10 },
            },
          }}
          // pageSizeOptions={[10, 10]}
          // checkboxSelection
        />
      </Box>
      {/* Delete modal */}
      <React.Fragment>
        <Dialog
          open={open}
          TransitionComponent={Transition}
          keepMounted
          onClose={handleClose}
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle>Warning</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              Are you sure you want to delete {open.name}?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleClose}
              style={{
                height: "80%",
                backgroundColor: "#ed1b24",
                marginRight: 10,
                marginLeft: 10,
                color: "#ffffff",
              }}
            >
              No
            </Button>
            <Button
              onClick={() => handleDelete(open.id)}
              style={{
                height: "80%",
                backgroundColor: "#ed1b24",
                marginRight: 10,
                marginLeft: 10,
                color: "#ffffff",
              }}
            >
              Yes
            </Button>
          </DialogActions>
        </Dialog>
      </React.Fragment>
    </Box>
  );
};

export default Users;
