import React, { useEffect, useState } from "react";
import {
  Modal,
  Box,
  Typography,
  Button,
  Table,
  TableContainer,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Paper,
  Link,
} from "@mui/material";
import date from "date-and-time";
import FolderIcon from "@mui/icons-material/Folder";
import axios from "axios";
import { API_PATHS, API_URL } from "../../utils/apiUtils";
const ContactCSVModal = ({ open, handleClose, data }) => {
  const { name, createdOn } = data;
  const [CSVData, setCSVData] = useState([]);

  useEffect(() => {
    (async function () {
      const res = await axios.get(API_PATHS.GET_ALL_CSV_FILES);
      setCSVData(res.data.result.reverse());
    })();
  }, [open]);

  return (
    <Modal open={open} onClose={handleClose}>
      <Box
        sx={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          bgcolor: "background.paper",
          boxShadow: 24,
          p: 4,
          width: 800,
          maxHeight: 600,
          overflowY: "scroll",
        }}
      >
        <Typography variant="h5" gutterBottom>
          Download CSV File
        </Typography>
        <TableContainer component={Paper}>
          <Table>
            <TableBody>
              <TableRow>
                <TableCell sx={{ fontWeight: 600 }}>Name</TableCell>

                <TableCell sx={{ fontWeight: 600 }}>Created On</TableCell>

                <TableCell sx={{ fontWeight: 600 }}>Downloads</TableCell>
              </TableRow>
              {CSVData.map((item) => (
                <TableRow>
                  <TableCell>
                    <Box display={"flex"} alignItems={"center"}>
                      <FolderIcon sx={{ color: "#6d6a6a", marginRight: 1 }} />
                      {item.name}
                    </Box>
                  </TableCell>
                  <TableCell>
                    {date.format(
                      new Date(item.created_on),
                      "ddd, MMM DD YYYY hh:mm A"
                    )}
                  </TableCell>
                  <TableCell>
                    <a href={`${API_URL}/${item.path}`}>Download</a>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Box>
    </Modal>
  );
};

export default ContactCSVModal;
