import * as React from "react";
import PropTypes from "prop-types";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import InboxIcon from "@mui/icons-material/MoveToInbox";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import MailIcon from "@mui/icons-material/Mail";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import language from "../../utils/language";
import { useNavigate } from "react-router-dom";

const drawerWidth = 200;

function Sidebar(props) {
  const navigateTo = useNavigate();
  const auth = JSON.parse(sessionStorage.getItem("user"));
  const role = auth.role;

  // const menuArray =
  //   role === "supervision"
  //     ? [
  //         { name: language.addNewResident, link: "/create-resident" },
  //         { name: language.seachEditResident, link: "/search-resident" },
  //         { name: language.users, link: "/users" },
  //       ]
  //     : role === "nurse"
  //     ? [{ name: language.seachEditResident, link: "/search-resident" }]
  //     : role === "superadmin"
  //     ? [
  //         { name: language.nursingHome, link: "/nursing-home" },
  //         { name: language.users, link: "/users" },
  //       ]
  //     : role === "administrative"
  //     ? [
  //         { name: language.addNewResident, link: "/create-resident" },
  //         { name: language.seachEditResident, link: "/search-resident" },
  //         { name: language.users, link: "/users" },
  //       ]
  //     : [];
  const menuArray = [
    { name: "Categories", link: "/categories" },
    { name: "Product sub categories", link: "/sub-category" },
    { name: "Products", link: "/products" },
    { name: "Contacted List", link: "/contacted-list" },
    { name: "Career", link: "/career-list" },
    // { name: "Transactions", link: "/transactions" },
    // { name: "Notifications", link: "/notifications" },
  ];
  const handleButtonClicked = (link) => {
    navigateTo(link);
  };
  const drawer = (
    <div>
      <Toolbar variant="dense">
        {/* <IconButton
          edge="start"
          color="inherit"
          aria-label="menu"
          sx={{ mr: 2 }}
        >
          <MenuIcon />
        </IconButton> */}
        <Typography variant="h6" color="inherit" component="div">
          Menu
        </Typography>
      </Toolbar>
      <Divider />
      <List>
        {menuArray.map((data, index) => (
          <ListItem key={data.name} disablePadding>
            <ListItemButton onClick={() => handleButtonClicked(data.link)}>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              <ListItemText primary={data.name} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      {/* <Divider /> */}
      {/* <List>
        {["All mail", "Trash", "Spam"].map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {index % 2 === 0 ? <InboxIcon /> : <MailIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List> */}
    </div>
  );

  return (
    <Drawer
      variant="permanent"
      sx={{
        "& .MuiDrawer-paper": {
          boxSizing: "border-box",
          width: drawerWidth,
          position: "static",
          height: "calc(100vh - 64px)",
        },
      }}
      open
    >
      {drawer}
    </Drawer>
  );
}

Sidebar.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

export default Sidebar;
