import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import MainPage from "./components/MainPage";

import "./App.css";
import Login from "./components/Login/Login";
import PrivateComponent from "./components/PrivateComponent";
import Register from "./components/Login/Register";
import Users from "./components/Users";
import RegisterSuperAdmin from "./components/Login/RegisterSuperAdmin";

import Layout from "./components/Common/Layout";

import Referrals from "./components/Referrals";
import InterestedProperties from "./components/InterestedProperties";
import Transactions from "./components/Transactions";
import UserDetail from "./components/UserDetail";
import TermsAndConditions from "./components/TermsAndConditions";
import SuggestedProperties from "./components/SuggestedProperties";
import NotificationsPage from "./components/NotificationsPage";
import { AddProduct } from "./components/AddProduct";
import { EditProduct } from "./components/EditProduct";
import { AddCategory } from "./components/AddCategory";
import { EditCategory } from "./components/EditCategory";
import { AddSubCategory } from "./components/AddSubCategory";
import { EditSubCategory } from "./components/EditSubCategory";
import Careers from "./components/Careers";

function App() {
  return (
    <Router>
      <Routes>
        <Route element={<PrivateComponent />}>
          <Route element={<Layout />}>
            <Route path="/" element={<MainPage />} />
            <Route path="/register" element={<Register />} />
            <Route
              path="/register-superadmin"
              element={<RegisterSuperAdmin />}
            />
            <Route path="/sub-category" element={<Users />} />
            {/* <Route path="/tnc" element={<TermsAndConditions />} /> */}
            <Route path="/userDetail" element={<UserDetail />} />
            <Route path="/categories" element={<Referrals />} />
            <Route path="/interested" element={<InterestedProperties />} />
            <Route path="/contacted-list" element={<SuggestedProperties />} />
            <Route path="/career-list" element={<Careers />} />
            <Route path="/notifications" element={<NotificationsPage />} />
            <Route path="/products" element={<Transactions />} />
            <Route path="/add-product" element={<AddProduct />} />
            <Route path="/add-category" element={<AddCategory />} />
            <Route path="/add-sub-category" element={<AddSubCategory />} />
            <Route path="/edit-product/:id" element={<EditProduct />} />
            <Route path="/edit-category/:id" element={<EditCategory />} />
            <Route
              path="/edit-sub-category/:id"
              element={<EditSubCategory />}
            />
          </Route>
        </Route>
        <Route path="/login" element={<Login />} />
      </Routes>
    </Router>
  );
}

export default App;
