import {
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Grid,
  IconButton,
  Modal,
  Slide,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import React, { useEffect, useState } from "react";
import { DataGrid } from "@mui/x-data-grid";
import DoneIcon from "@mui/icons-material/Done";
import { Clear } from "@mui/icons-material";
import RewardModal from "./Common/RewardModal";
import RejectReferralModal from "./Common/RejectReferralModal";
import { API_PATHS, API_URL } from "../utils/apiUtils";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import date from "date-and-time";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import CustomTextField from "./Common/CustomTextField";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const PatientForm1 = () => {
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "auto",
    height: "60vh",
    // overflowY: "scroll",
    bgcolor: "background.paper",
    // border: "1px solid #000",
    boxShadow: 24,
    borderRadius: "7px",
    p: 3,
  };

  const navigate = useNavigate();

  const [isModalOpen, setModalOpen] = useState(false);
  const [isModalOpen1, setModalOpen1] = useState(false);
  const [referralRow, setReferralRow] = useState([]);
  const [allData, setAllData] = useState([]);
  const [amount, setAmount] = useState();
  const [selectedRowId, setSelectedRowId] = useState();
  const [referId, setReferId] = useState();
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState("");
  // const [rewardAmount, setRewardAmount] = useState("");
  const [referReason, setReferReason] = useState("Refer");
  const [selectedOption, setSelectedOption] = useState("Pending"); // Default selected option
  const [textModal, setTextModal] = useState("");
  const [loading, setLoading] = useState(false);

  const [searchText, setSearchText] = useState("");
  const [open, setOpen] = React.useState(false);

  const closeTextModal = () => setTextModal("");
  const openTextModal = ({ row }) => {
    const timestamp = row.referred_on;
    const dateObj = new Date(timestamp);

    const day = String(dateObj.getDate()).padStart(2, "0");
    const month = String(dateObj.getMonth() + 1).padStart(2, "0"); // Months are zero-based
    const year = dateObj.getFullYear();
    const hours = String(dateObj.getHours()).padStart(2, "0");
    const minutes = String(dateObj.getMinutes()).padStart(2, "0");

    const formattedDate = `${day}-${month}-${year}, ${hours}:${minutes}`;
    setTextModal({ ...row, referred_on: formattedDate });
  };

  const navigateTo = useNavigate();

  useEffect(() => {
    getAllCategories();
  }, []);

  useEffect(() => {
    const filteredData = allData.filter((item) =>
      item.name.toLowerCase().includes(searchText.toLowerCase())
    );

    setReferralRow(filteredData);
  }, [searchText]);

  const openModal1 = () => {
    setModalOpen1(true);
  };

  const closeModal1 = () => {
    setModalOpen1(false);
  };

  const handleClickOpen = (id, name) => {
    setOpen({ name, id });
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleConfirmYes = () => {
    // Handle the "Yes" button click here.
    // You can add your logic for declining the referral.
    // For example, you can make an API request to decline the referral.
    console.log("Declining referral...");
    closeModal1(); // Close the modal after confirming.
  };

  const openModal = () => {
    setModalOpen(true);
  };

  const closeModal = () => {
    setModalOpen(false);
  };

  const handleSnackbarClose = () => {
    setSnackbarOpen(false);
  };

  const handleDelete = async (id) => {
    console.log("delete icon clicked-->", id);
    try {
      const res = await axios.delete(`${API_PATHS.DELETE_CATEGORY}/${id}`);
      handleClose();
      setTimeout(() => {
        alert("Successfully deleted.");
        getAllCategories();
      }, 500);
    } catch (err) {
      handleClose();
      setTimeout(() => {
        alert("error, while deleting the category!");
      }, 500);
    }
  };

  const getAllCategories = async () => {
    try {
      console.log("Url Path-->", API_PATHS.GET_ALL_CATEGORIES);
      setLoading(true);
      const response = await axios.get(API_PATHS.GET_ALL_CATEGORIES);

      const resArr = response.data.result.map((item, index) => {
        return {
          sno: index + 1,
          databaseId: item._id,
          id: `CAT${item._id.slice(-5)}`,
          _id: item._id,
          name: item.name,
          file_name: item.file_name ? item.file_name : "NA",
          master_folder_name: item.master_folder_name
            ? item.master_folder_name
            : "NA",
          product_folder_name: item.product_folder_name
            ? item.product_folder_name
            : "NA",
          created_on: date.format(
            new Date(item.created_on),
            "ddd, MMM DD YYYY hh:mm A"
          ),
        };
      });
      console.log("dfsfsdf", resArr);
      setReferralRow(resArr);
      setAllData(resArr);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      console.log("Error In get all referrals-->", error);
    }
  };

  const exportCSV = (rows, columns) => {
    const csvRows = [];

    // Create the header row
    const exportedColumns = columns.filter((item) => !item.hideInExport);
    const header = exportedColumns.map((column) => column.headerName);
    csvRows.push(header.join(","));

    // Create the data rows
    rows.forEach((row) => {
      const rowData = exportedColumns.map((column) => {
        return row[column.field];
      });
      csvRows.push(rowData.join(","));
    });

    // Combine rows into a single CSV string
    const csvString = csvRows.join("\n");

    // Create a blob containing the CSV data
    const blob = new Blob([csvString], { type: "text/csv" });

    // Create a temporary URL to the blob
    const url = window.URL.createObjectURL(blob);

    // Create a link element to trigger the download
    const a = document.createElement("a");
    a.href = url;
    a.download = "categories_data.csv";

    // Trigger the click event on the link to initiate the download
    a.click();

    // Clean up the temporary URL
    window.URL.revokeObjectURL(url);
  };

  const columns = [
    {
      field: "action",
      headerName: "Action",
      sortable: false,
      renderCell: ({ row }) => (
        <>
          <IconButton onClick={() => navigate(`/edit-category/${row._id}`)}>
            <EditIcon color="red" />
          </IconButton>
          <IconButton onClick={() => handleClickOpen(row._id, row.name)}>
            <DeleteIcon color="red" />
          </IconButton>
        </>
      ),
    },

    { field: "sno", headerName: "S.No", width: 70 },
    {
      field: "icon",
      headerName: "Icon",
      sortable: false,
      renderCell: ({ row }) => (
        <div>
          <img
            src={`${API_URL}/${row.master_folder_name}/${row.file_name}`}
            style={{ borderRadius: "50%" }}
            width={40}
            height={40}
          />
        </div>
      ),
    },
    { field: "id", headerName: "ID", width: 100 },
    { field: "name", headerName: "Name", width: 150 },
    { field: "file_name", headerName: "File name", width: 150 },
    {
      field: "master_folder_name",
      headerName: "Master folder name",
      width: 250,
    },
    {
      field: "product_folder_name",
      headerName: "Product folder name",
      width: 300,
    },

    {
      field: "created_on",
      headerName: "Created on",
      width: 250,
    },
  ];

  // columns.unshift({
  //   field: "action",
  //   headerName: "Amount",
  //   width: 150,
  //   sortable: true,
  //   renderCell: ({ row }) => {
  //     return (
  //       <div
  //         style={{
  //           display: "flex",
  //           justifyContent: "space-between",
  //           width: "100%",
  //           alignItems: "center",
  //         }}
  //       >
  //         <TextField
  //           label=""
  //           variant="outlined"
  //           fullWidth
  //           type="number"
  //           onClick={(event) => event.stopPropagation()}
  //           disabled={row.referralStatus !== "Converted"}
  //           InputProps={{ style: { height: "40px" } }}
  //           onBlur={(event) =>
  //             handleOnBlur(row.databaseId, row.referredBy, event)
  //           }
  //         />
  //         {/* <DoneIcon
  //           style={{ color: "#50C878", cursor: "pointer" }}
  //           onClick={(event) =>
  //             handleDoneClick(row.databaseId, event, row.referredBy)
  //           }
  //         />
  //         <Clear
  //           style={{ color: "red", cursor: "pointer" }}
  //           onClick={(event) =>
  //             handleCrossClick(row.databaseId, event, row.referred_by)
  //           }
  //         /> */}
  //       </div>
  //     );
  //   },
  // });

  return (
    <Box sx={{ width: "85%", m: 2 }}>
      {/* <Box
        sx={{
          flexDirection: "row-reverse",
          display: "flex",
        }}
      >
        <Button variant="contained">Add</Button>
      </Box> */}

      <Snackbar
        open={snackbarOpen}
        autoHideDuration={1000} // Adjust the duration as needed
        onClose={handleSnackbarClose}
        message={snackbarMessage}
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
      />
      <Box
        sx={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography fontSize={40}>Categories</Typography>

        <div>
          <CustomTextField
            placeholder="Search..."
            onChange={(e) => setSearchText(e.target.value)}
          />
          <Button
            variant="contained"
            onClick={() => {
              navigate("/add-category");
            }}
            style={{
              height: "80%",
              backgroundColor: "#ed1b24",
              marginRight: 10,
              marginLeft: 10,
            }}
          >
            Add Category
          </Button>
          <Button
            variant="contained"
            onClick={() => exportCSV(referralRow, columns)}
            style={{ height: "80%", backgroundColor: "#ed1b24" }}
          >
            Export To CSV
          </Button>
        </div>
      </Box>
      <Box sx={{ width: "100%" }}>
        <DataGrid
          // sx={{ overflowX: "scroll", width: "100%" }}
          autoHeight
          loading={loading}
          rows={referralRow}
          columns={columns}
          onCellClick={(params) => {
            if (params.field === "__check__" || params.field === "action") {
              return;
            }
            openTextModal(params);
          }}
          initialState={{
            pagination: {
              paginationModel: { page: 0, pageSize: 10 },
            },
          }}
          // pageSizeOptions={[10, 10]}
          // checkboxSelection
        />
      </Box>
      <Modal open={textModal} onClose={closeTextModal}>
        <Box sx={style}>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            Name: {textModal.name}
          </Typography>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            File name: {textModal.file_name}
          </Typography>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            Master folder name: {textModal.master_folder_name}
          </Typography>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            Product folder name: {textModal.product_folder_name}
          </Typography>
          <Typography id="modal-modal-description" variant="h6" sx={{ mt: 2 }}>
            Name: {textModal.name}
          </Typography>
        </Box>
      </Modal>
      {/* Delete modal */}
      <React.Fragment>
        <Dialog
          open={open}
          TransitionComponent={Transition}
          keepMounted
          onClose={handleClose}
          aria-describedby="alert-dialog-slide-description"
        >
          <DialogTitle>Warning</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-slide-description">
              Are you sure you want to delete {open.name}?
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleClose}
              style={{
                height: "80%",
                backgroundColor: "#ed1b24",
                marginRight: 10,
                marginLeft: 10,
                color: "#ffffff",
              }}
            >
              No
            </Button>
            <Button
              onClick={() => handleDelete(open.id)}
              style={{
                height: "80%",
                backgroundColor: "#ed1b24",
                marginRight: 10,
                marginLeft: 10,
                color: "#ffffff",
              }}
            >
              Yes
            </Button>
          </DialogActions>
        </Dialog>
      </React.Fragment>
      {/* <RewardModal
        open={isModalOpen}
        handleClose={closeModal}
        handleSubmit={handleSubmit}
      />
      <RejectReferralModal
        open={isModalOpen1}
        handleClose={closeModal1}
        handleConfirm={handleConfirmYes}
      /> */}
    </Box>
  );
};

export default PatientForm1;
